<template>
    <b-row class="mt-2">
        <b-table striped hover
            :items="tableObjects" 
            :fields="getTableHeaderRequirements"
            fixed
            responsive
            primary-key="id">
            <template #cell(actions)="row">
                <b-button size="sm" variant="outline-dark" @click="row.toggleDetails" class="mr-2">
                    <b-icon-bezier></b-icon-bezier>
                </b-button>
                <b-button size="sm" variant="outline-dark" @click="showRequirementDetails(row.item.id)"><b-icon-gear></b-icon-gear></b-button>
                <basic-button-delete
                    :itemId="row.item.id"
                    @deleteItem="deleteRequirement" />
            </template>
            <template #cell(flags)="row">
                <b-badge class="bg-primary ml-1">{{ (hasParent(row.item.id) ? "hat Eltern" : "") }}</b-badge>
                <b-badge class="bg-primary ml-1">{{ (hasChildren(row.item.id) ? "hat Kinder" : "") }}</b-badge>
                <b-badge class="bg-primary ml-1">{{ (notUsed(row.item.id) ? "nicht verwendet" : "") }}</b-badge>
                <b-badge class="bg-primary ml-1">{{ (hasPredecessor(row.item.id) ? "hat Vorgänger" : "") }}</b-badge>
                <b-badge class="bg-primary ml-1">{{ (hasSuccessor(row.item.id) ? "hat Nachfolger" : "") }}</b-badge>
                <b-badge class="bg-info ml-1" v-for="label in row.item.requirementLabels" :key="label">{{ label }}</b-badge>
            </template>
            <template #cell(type)="row">
                <basic-cell-select-badge
                    :selected="row.item.requirementType"
                    :options="requirementTypes"
                    :dataObjectId="row.item.id"
                    @changeItem="changeType" />
            </template>
            <template #cell(tablet)="row">
                <basic-cell-select-badge
                    :selected="row.item.requirementTablet"
                    :options="ddTabletTypes"
                    :dataObjectId="row.item.id"
                    @changeItem="changeTablet" />
            </template>
            <template #cell(name)="row">
                <basic-cell-textarea 
                    :textValue="row.item.name"
                    :dataObjectId="row.item.id"
                    @changeItem="changeTitle" />
            </template>
            <template #cell(description)="row">
                <basic-cell-textarea 
                    :textValue="row.item.description"
                    :dataObjectId="row.item.id"
                    @changeItem="changeDesc" />
            </template>
            <template #cell(benefit)="row">
                <basic-cell-textarea 
                    :textValue="row.item.requirementBenefit"
                    :dataObjectId="row.item.id"
                    @changeItem="changeBenefit" />
            </template>
            <template #cell(source)="row">
                <b-form-select v-model="row.item.requirementSource" :options="ddLayerElements" @change="changeSource(row.item.id, row.item.requirementSource)"></b-form-select>
            </template>
            <template #cell(commitment)="row">
                <basic-cell-select-badge
                    :selected="row.item.requirementCommitment"
                    :options="commitmentTypes"
                    :dataObjectId="row.item.id"
                    @changeItem="changeCommitment" />
            </template>
            <template #cell(sourcedetails)="row">
                <basic-cell-textarea 
                    :textValue="row.item.requirementSourceDetails"
                    :dataObjectId="row.item.id"
                    @changeItem="changeDetails" />
            </template>
            <template #row-details="row">
                <my-card-requirement-realizes-children
                    :requirementId="row.item.id" />
            </template>
        </b-table>
    </b-row>
</template>

<script>
import BasicCellSelectBadge from './BasicCellSelectBadge.vue'
import BasicCellTextarea from './BasicCellTextarea.vue'
import BasicButtonDelete from './BasicButtonDelete.vue'
import MyCardRequirementRealizesChildren from './MyCardRequirementRealizesChildren.vue'
import { mapGetters, mapMutations } from 'vuex'

export default {
    props: {
        tableObjects: {
            type: Array,
            required: true
        }
    },

    components: {
        BasicCellSelectBadge,
        BasicCellTextarea,
        BasicButtonDelete,
        MyCardRequirementRealizesChildren
    },

    computed: {
        ddLayerElements() {
            var layerElements = this.getddElementsGroupByType.filter(e => this.layers.indexOf(e.label) != -1)

            var noOption = [{ 
                label: "keine Auswahl",
                options: ['']
            }];

            return noOption.concat(layerElements);
        },

        ddTabletTypes() {
            var noOption = [{ 
                label: "keine Auswahl",
                options: ['']
            }];

            return noOption.concat(this.tabletTypes);
        },

        ...mapGetters([
            'getTableHeaderRequirements',
            'requirementTypes',
            'tabletTypes',
            'getddElementsGroupByType',
            'layers',
            'getRealizationsOfRequirement',
            'getParentIdByChildId',
            'getHierarchiesByParentId',
            'getPredecessorOfRequirement',
            'getSuccessorOfRequirement'
        ])
    },

    methods: {
        hasSuccessor(requirementId) {
            return this.getSuccessorOfRequirement(requirementId) != undefined
        },

        hasPredecessor(requirementId) {
            return this.getPredecessorOfRequirement(requirementId) != undefined
        },

        hasChildren(requirementId) {
            return this.getHierarchiesByParentId(requirementId).length > 0
        },
        
        hasParent(requirementId) {
            return this.getParentIdByChildId(requirementId).length > 0
        },

        notUsed(requirementId) {
            return this.getRealizationsOfRequirement(requirementId).length == 0
        },

        changeType(requirementId, newCellValue) {
            this.changeElementProp({
                id: requirementId,
                prop: 'requirementType',
                newValue: newCellValue
            })
        },

        changeTablet(requirementId, newCellValue) {
            this.changeElementProp({
                id: requirementId,
                prop: 'requirementTablet',
                newValue: newCellValue
            })
        },

        changeTitle(requirementId, newCellValue) {
            this.changeElementProp({
                id: requirementId,
                prop: 'name',
                newValue: newCellValue
            })
        },

        changeDesc(requirementId, newCellValue) {
            this.changeElementProp({
                id: requirementId,
                prop: 'description',
                newValue: newCellValue
            })
        },

        changeBenefit(requirementId, newCellValue) {
            this.changeElementProp({
                id: requirementId,
                prop: 'requirementBenefit',
                newValue: newCellValue
            })
        },
        changeDetails(requirementId, newCellValue) {
            this.changeElementProp({
                id: requirementId,
                prop: 'requirementSourceDetails',
                newValue: newCellValue
            })
        },

        changeCommitment(requirementId, newCellValue) {
            this.changeElementProp({
                id: requirementId,
                prop: 'requirementCommitment',
                newValue: newCellValue
            })
        },

        changeSource(requirementId, newCellValue) {
            this.changeElementProp({
                id: requirementId,
                prop: 'requirementSource',
                newValue: newCellValue
            })
        },

        deleteRequirement(elementId) {
            var realizations = this.getRealizationsOfRequirement(elementId)
            realizations.forEach((rel) => this.deleteRelationById(rel))
            this.deleteElementById(elementId)

        },

        showRequirementDetails(requirementId) {
            this.setSelectedRequirementById(requirementId)
            this.setSelectedRelationById("")
            this.setShowRelationModal(true)
        },

        ...mapMutations([
            'setSelectedRequirementById',
            'setSelectedRelationById',
            'setShowRelationModal',
            'deleteElementById',
            'deleteRelationById',
            'changeElementProp'
        ])
    }
}
</script>

<style scoped>

</style>


