<template>
    <div>
        <div v-show="!showEdit" @click="showEdit = true">{{ (textValue.length > 0 ? textValue : "...") }}</div>
        <b-button v-show="showEdit" class="m-1" size="sm" variant="outline-dark" @click="showEdit = false"><b-icon-arrow-left></b-icon-arrow-left></b-button>
        <b-form-textarea v-show="showEdit" rows="2" max-rows="6" v-model="textareaValue" @blur="changeItem()"></b-form-textarea>
    </div>
</template>

<script>
export default {
    props: {
        textValue: {
            type: String,
            required: true
        },
        dataObjectId: {
            type: String,
            required: true
        },
    },

    data() {
        return {
            showEdit: false,
            textareaValue: this.textValue
        }
    },

    methods: {
        changeItem() {
            this.$emit('changeItem', this.dataObjectId, this.textareaValue);
            this.showEdit = false;
        }
    }
}
</script>

<style scoped>

</style>


