<template>
    <b-card no-body class="mb-1" v-if="upperLayer.length > 0">
        <b-card-header header-tag="header" class="p-1" role="tab">
            <b-button block v-b-toggle.upper-info variant="outline-dark">
                <span><strong>Eltern Element</strong> :: Ebene {{ upperLayer }} <b-badge pill class="bg-secondary">{{ (upperElement.id != undefined ? "set" : "") }}</b-badge></span>
            </b-button>
        </b-card-header>
        <b-collapse id="upper-info" :visible="visible">
        <b-card-body>
        <div class="container-fluid">
            <basic-row-add-link v-show="upperElement.id == undefined"
                :ddItems="ddUpperLayerElements"
                buttonText="Element"
                @addItem="addParent"
                @linkItem="linkParent"
            />
            <div class="row mt-2">
                <div class="col-11">
                    <basic-row-id-name
                        :dataObject="upperElement"
                        :isEditable="false" />
                    <basic-row-input
                        rowLabel="Beschreibung"
                        :dataObject="upperElement"
                        objectProp="description"
                        :isEditable="false" />
                </div>
                <div class="col-1">
                    <b-button v-if="upperElement.id != undefined" class="mt-3" size="sm" variant="outline-dark" @click="showElementDetails(upperElement.id)"><b-icon-gear></b-icon-gear></b-button>
                    <my-button-unlink v-if="upperElement.id != undefined"
                        :item-id="upperElement.id"
                        @unlinkItem="removeParent"
                    />
                </div>
            </div>
        </div>
        </b-card-body>
        </b-collapse>
    </b-card>
</template>

<script>
import { mapGetters, mapMutations } from 'vuex'
import BasicRowAddLink from './BasicRowAddLink.vue'
import BasicRowIdName from './BasicRowIdName.vue'
import BasicRowInput from './BasicRowInput.vue'
import MyButtonUnlink from './MyButtonUnlink.vue'

export default {
    props: {
        visible: {
            type: Boolean,
            required: false,
            default: true
        }
    },

    components: {
        BasicRowAddLink,
        BasicRowIdName,
        BasicRowInput,
        MyButtonUnlink
    },

    computed:{
        upperLayer() {
            var indexDisplayedLayer = this.layers.indexOf(this.getSelectedElement.type);
            
            if(indexDisplayedLayer > 0) {
                return this.layers[indexDisplayedLayer -1];
            }
            return "";
        },

        upperElement() {
            var parentId = this.getParentIdByChildId(this.getSelectedElement.id)
            if(parentId.length > 0) {
                return this.getElementById(parentId)
            }
            return {}
        },

        ddUpperLayerElements() {
            var layerElements = this.getddElementsGroupByType.find(el => el.label == this.upperLayer)
            if(layerElements != undefined) {
                return layerElements.options
            }
            return []
        },

        ...mapGetters([
            'getSelectedElement',
            'layers',
            'getParentIdByChildId',
            'getElementById',
            'getddElementsGroupByType',
            'getLastAddedElement'
        ])
    },

    methods: {
        linkParent(elementId) {
            this.addHierarchyObject({
                parentId: elementId,
                childId: this.getSelectedElement.id
            })
        },

        addParent(elementName) {
            this.addElement({
                name: elementName,
                type: this.upperLayer
            })
            var addedElement = this.getLastAddedElement
            this.addHierarchyObject({
                parentId: addedElement.id,
                childId: this.getSelectedElement.id
            })
        },

        removeParent(elemId) {
            this.removeHierarchyObject({
                parentId: elemId,
                childId: this.getSelectedElement.id
            })
        },

        showElementDetails(elemId) {
            this.setSelectedElementById(elemId)
        },

        ...mapMutations([
            'addElement',
            'addHierarchyObject',
            'setSelectedElementById',
            'removeHierarchyObject'
        ])
    }
}
</script>

<style scoped>

</style>


