<template>
    <b-card no-body class="mb-1" v-if="childLayer.length > 0">
        <b-card-header header-tag="header" class="p-1" role="tab">
            <b-button block v-b-toggle.lower-info variant="outline-dark">
                <span><strong>Kinder Elemente</strong> :: Ebene {{ childLayer }} <b-badge pill class="bg-secondary">{{ (elementChildren.length > 0 ? elementChildren.length : "") }}</b-badge></span>
            </b-button>
        </b-card-header>
        <b-collapse id="lower-info" :visible="visible">
        <b-card-body>
        <b-container fluid>
            <basic-row-add-link
                :ddItems="ddLayerElements"
                buttonText="Element"
                @addItem="addChild"
                @linkItem="linkChild"
            />
            <b-row class="mt-2" v-for="elem in elementChildren" :key="elem.id">
                <b-col cols="11">
                    <b-card no-body class="m-1 p-2">
                        <basic-row-id-name
                            :dataObject="elem"
                            :isEditable="false" />
                        <basic-row-start-ende v-if="elem.isRelease"
                            startProp="releaseStart"
                            endProp="releaseEnd"
                            :dataObject="elem"
                            @changeElement="changeElement" />
                    </b-card>
                </b-col>
                <b-col cols="1">
                    <b-button class="mt-1" size="sm" variant="outline-dark" @click="showElementDetails(elem.id)"><b-icon-gear></b-icon-gear></b-button>
                    <my-button-unlink
                        :itemId="elem.id"
                        @unlinkItem="removeChild" />
                </b-col>
            </b-row>
        </b-container>
        </b-card-body>
        </b-collapse>
    </b-card>
</template>

<script>
import MyButtonUnlink from './MyButtonUnlink.vue'
import BasicRowAddLink from './BasicRowAddLink.vue'
import BasicRowStartEnde from './BasicRowStartEnde.vue'
import BasicRowIdName from './BasicRowIdName.vue'
import { mapGetters, mapMutations } from 'vuex'

export default {
    props: {
        visible: {
            type: Boolean,
            required: false,
            default: true
        }
    },

    components: {
        MyButtonUnlink,
        BasicRowAddLink,
        BasicRowStartEnde,
        BasicRowIdName
    },

    data() {
        return {
        }
    },

    computed: {
        childLayer() {
            var layers = this.layers;
            var indexDisplayedLayer = layers.indexOf(this.getSelectedElement.type);
            
            if(indexDisplayedLayer < (layers.length -1)) {
                return layers[indexDisplayedLayer +1];
            }
            return "";
        },

        elementChildren() {
            if(this.childLayer.length > 0) {
                return this.getHierarchiesByParentId(this.getSelectedElement.id)
                    .map(h => this.getElementById(h.childId))
            }
            return []
        },

        ddLayerElements() {
            var layerObject = this.getddElementsGroupByType.find(grp => grp.label == this.childLayer)
            if(layerObject != undefined) {
                var layerElements = this.getddElementsGroupByType.find(grp => grp.label == this.childLayer).options
                var allChildIds = this.getAllHierarchies.map(e => e.childId)
                var hasNoParentElements = layerElements.filter(e => allChildIds.indexOf(e.value) == -1)
                return hasNoParentElements
            }
            return []
        },

        ...mapGetters([
            'layers',
            'getSelectedElement',
            'getHierarchiesByParentId',
            'getElementById',
            'getddElementsGroupByType',
            'getAllHierarchies',
            'getLastAddedElement'
        ])
    },

    methods: {
        changeElement(elemId, elemProp, newElemValue) {
            this.changeElementProp({
                id: elemId,
                prop: elemProp,
                newValue: newElemValue
            })
        },

        removeChild(childId) {
            this.removeHierarchyObject({
                parentId: this.getSelectedElement.id,
                childId: childId
            })
        },

        showElementDetails(elemId) {
            this.setSelectedElementById(elemId)
        },

        linkChild(elementId) {
            if(this.childLayer.length > 0) {
                this.addHierarchyObject({
                    parentId: this.getSelectedElement.id,
                    childId: elementId
                })
            }
        },

        addChild(elementName) {
            if(this.childLayer.length > 0) {
                this.addElement({
                    name: elementName,
                    type: this.childLayer
                })
                var addedElement = this.getLastAddedElement
                this.addHierarchyObject({
                    parentId: this.getSelectedElement.id,
                    childId: addedElement.id
                })
            }
        },

        ...mapMutations([
            'changeElementProp',
            'addHierarchyObject',
            'addElement',
            'setSelectedElementById',
            'removeHierarchyObject'
        ])
    }
}
</script>

<style scoped>

</style>


