<template>
    <b-card no-body class="mb-1">
        <b-card-header header-tag="header" class="p-1" role="tab">
            <b-button block v-b-toggle.requirement-info variant="outline-dark">Requirement :: Allgemein</b-button>
        </b-card-header>
        <b-collapse id="requirement-info" accordion="modal-accordion" role="tabpanel">
        <b-card-body>
        <b-container fluid>
            <b-row class="mt-2 mb-2"><small><b-alert variant="info" show><u>Hinweis:</u> Änderungen werden für alle Realisierungen übernommen</b-alert></small></b-row>
            <basic-row-input
                rowLabel="Requirement-Id"
                :dataObject="getSelectedRequirement"
                objectProp="id"
                :isEditable="false" />
            <basic-row-dropdown-item
                rowLabel="Quelle"
                :ddElements="ddLayerElements"
                :selectedItemId="getSelectedRequirement.requirementSource"
                @changeItem="changeSource" />
            <basic-row-input
                rowLabel="Quellen-Details"
                :dataObject="getSelectedRequirement"
                objectProp="requirementSourceDetails"
                @propChanged="itemChanged" />
            <basic-row-select
                rowLabel="Tablet"
                :dataObject="getSelectedRequirement"
                objectProp="requirementTablet"
                :propValues="ddTabletTypes"
                @propChanged="itemChanged" />
            <basic-row-select
                rowLabel="Typ"
                :dataObject="getSelectedRequirement"
                objectProp="requirementType"
                :propValues="requirementTypes"
                @propChanged="itemChanged" />
            <basic-row-textarea
                rowLabel="Titel"
                :dataObject="getSelectedRequirement"
                objectProp="name"
                @propChanged="itemChanged" />
            <basic-row-textarea
                rowLabel="Beschreibung"
                :dataObject="getSelectedRequirement"
                objectProp="description"
                @propChanged="itemChanged" />
            <basic-row-input
                rowLabel="Mehrwert"
                :dataObject="getSelectedRequirement"
                objectProp="requirementBenefit"
                @propChanged="itemChanged" />
            <basic-row-tags
                rowLabel="Labels"
                :dataObject="getSelectedRequirement"
                objectProp="requirementLabels"
                @propChanged="tagsChanged" />
        </b-container>
        </b-card-body>
        </b-collapse>
    </b-card>
</template>

<script>
import { mapGetters, mapMutations } from 'vuex'
import BasicRowInput from './BasicRowInput.vue'
import BasicRowSelect from './BasicRowSelect.vue'
import BasicRowTextarea from './BasicRowTextarea.vue'
import BasicRowDropdownItem from './BasicRowDropdownItem.vue'
import BasicRowTags from './BasicRowTags.vue'

export default {
    components: { 
        BasicRowInput,
        BasicRowSelect,
        BasicRowTextarea,
        BasicRowDropdownItem,
        BasicRowTags
    },

    computed: {
        ddLayerElements() {
            var layerElements = this.getddElementsGroupByType.filter(e => this.layers.indexOf(e.label) != -1)

            var noOption = [{ 
                label: "keine Auswahl",
                options: ['']
            }];

            return noOption.concat(layerElements);
        },

        ddTabletTypes() {
            var noOption = [{ 
                label: "keine Auswahl",
                options: ['']
            }];

            return noOption.concat(this.tabletTypes);
        },

        ...mapGetters([
            'getSelectedRequirement',
            'getddElementsGroupByType',
            'tabletTypes',
            'requirementTypes',
            'layers'
        ])
    },

    methods: {
        itemChanged(objectId, newVal, itemProp) {
            this.changeElementProp({
                id: objectId,
                prop: itemProp,
                newValue: newVal
            })
        },

        tagsChanged(objectId, newVal, itemProp) {
            newVal.forEach((t) => {
                this.addLabel(t)
            })
            this.changeElementProp({
                id: objectId,
                prop: itemProp,
                newValue: newVal
            })
        },

        changeSource(sourceId) {
            this.changeElementProp({
                id: this.getSelectedRequirement.id,
                prop: 'requirementSource',
                newValue: sourceId
            })
        },

        ...mapMutations([
            'changeElementProp',
            'addLabel'
        ])
    }
}
</script>

<style scoped>

</style>


