<template>
    <b-modal 
        v-model="showModal" 
        scrollable
        size="xl"
        ok-only
        :title="'Element Details'"
        @hide="hideModal"
        @blur="hideModal"
        @close="hideModal">
            <b-button v-show="getSelectedElementIdHistory.length > 0" class="m-1 mb-2" size="sm" variant="outline-dark" @click="showLastElementDetails()"><b-icon-arrow-left></b-icon-arrow-left></b-button>
            <my-card-selected-element-inherit-values
                :visible="true" />
            <my-card-selected-element-parent
                :visible="true" />
            <my-card-selected-element-general
                :visible="true" />
            <my-card-selected-element-siblings
                :visible="false" />
            <my-card-selected-element-children
                :visible="true" />
    </b-modal>
</template>

<script>
import { mapGetters, mapMutations } from 'vuex'
import MyCardSelectedElementGeneral from './MyCardSelectedElementGeneral.vue'
import MyCardSelectedElementParent from './MyCardSelectedElementParent.vue'
import MyCardSelectedElementChildren from './MyCardSelectedElementChildren.vue'
import MyCardSelectedElementSiblings from './MyCardSelectedElementSiblings.vue'
import MyCardSelectedElementInheritValues from './MyCardSelectedElementInheritValues.vue'

export default {
    components: {
        MyCardSelectedElementChildren,
        MyCardSelectedElementSiblings,
        MyCardSelectedElementGeneral,
        MyCardSelectedElementParent,
        MyCardSelectedElementInheritValues,
    },
    computed: {
        ...mapGetters([
            'getShowElementModal',
            'getSelectedElementIdHistory'
        ]),

        showModal: {
            get() {
                return this.getShowElementModal
            },
            set(newVal) {
                this.setShowElementModal(newVal)
            }
        },
    },

    methods: {
        hideModal() {
            this.resetSelectedElementIdHistory
            this.showModal = false
        },

        showLastElementDetails() {
            this.selectedLastElement()
        },

        ...mapMutations([
            'resetSelectedElementIdHistory',
            'selectedLastElement',
            'setShowElementModal'
        ]),
    },
}
</script>

<style scoped>

</style>


