<template>
    <div class="row mt-1">
        <div class="container-fluid mb-2">
            <div class="row mb-2">
                <div class="col-2"></div>
                <div class="col-8"><b-form-input type="text" v-model="functionName" :disabled="!showEdit"></b-form-input></div>
                <div class="col-2">
                    <b-button v-show="!showEdit" class="mt-1" size="sm" variant="outline-dark" @click="showEdit = !showEdit"><b-icon-gear></b-icon-gear></b-button>
                    <b-button v-show="showEdit" class="mt-1" size="sm" variant="outline-dark" @click="changeFunctionName()"><b-icon-check></b-icon-check></b-button>
                    <my-button-unlink
                        :item-id="this.getSelectedElement.id"
                        @unlinkItem="removeFunctionRelation()"
                    />
                </div>
            </div>
            <div class="row mt-1" v-show="showEdit" v-for="elem in elementsOfFunction" :key="elem.id">
                <div class="col-4"><small>auch bereitgestellt durch</small></div>
                <div class="col-6">
                    <b-form-input type="text" :value="elem.name" disabled></b-form-input>
                </div>
                <div class="col-2">
                    <b-button class="mt-1" size="sm" variant="outline-dark" @click="showElementDetail(elem.id)"><b-icon-gear></b-icon-gear></b-button>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import { mapGetters, mapMutations } from 'vuex'
import MyButtonUnlink from './MyButtonUnlink.vue';

export default {
    props: {
        functionId: {
            type: String,
            required: true
        }
    },

    components: { 
        MyButtonUnlink 
    },

    data() {
        return {
            showEdit: false,
            functionName: this.getElementById(this.functionId).name
        }
    },

    computed: {
        ddFunctions() {
            return this.elements.filter(e => e.type == 'Applikationsfunktion').map((func) => ({value: func.id, text: func.name}));
        },

        elementsOfFunction() {
            return this.getElementsByFunctionId(functionId).filter(e => e.id != this.getSelectedElement.id)
        },

        ...mapGetters([
            'getElementsByFunctionId',
            'getSelectedElement',
            'getElementById'
        ])
    },

    methods: {
        removeFunctionRelation() {
            var newFunctionArray = this.getSelectedElement.functions.filter(f => f != this.functionId)
            this.changeElementProp({
                id: this.getSelectedElement.id,
                prop: 'functions',
                newValue: newFunctionArray
            })
            this.showEdit = !this.showEdit;
        },

        changeFunctionName() {
            this.changeElementProp({
                id: this.functionId,
                prop: 'name',
                newValue: this.functionName
            })
            this.showEdit = !this.showEdit;
        },

        showElementDetails(elemId) {
            this.setSelectedElementById(elemId)
        },

        ...mapMutations([
            'setSelectedElementById',
            'changeElementProp'
        ])
    }

}
</script>

<style scoped>

</style>


