<template>
    <b-row>
        <b-container class="m-2" fluid>
            <b-row class="mb-4">
                <b-col class="form-check form-check-inline">
                    <small class="form-text text-muted">
                        Interaktives Diagramm:
                        <p>
                            Einfach-<b-icon-mouse></b-icon-mouse>
                            :: Eingrenzung der Darstellung und Tabelle auf Selektion
                            (Mehrfachselektion möglich)
                        </p>
                    </small>
                </b-col>
            </b-row>
            <b-row class="w-75 mb-4">
                <basic-svg-bubble-packing
                    :circleNodes="graphNodes"
                    :selectedKeys="getScopedElementIds"
                    :consumeKeys="getScopedConsumeNodes"
                    :dependencyKeys="getScopedDependencyNodes"
                    :svgwidth="graphWidth"
                    :svgheight="graphHeight"
                    :paddingNodes="3"
                    :legendNameFill="relationTypes"
                    keyProperty="id"
                    displayProperty="name"
                    @nodeClick="addScopeElementId" />
                <b-col sm>
                    <b-card title="Diagramm" class="p-3 mb-3">
                        <b-card-text>
                            <small>Gruppierung:</small>
                            <b-form-select v-model="selectedGrouping" :options="layers"></b-form-select>
                        </b-card-text>
                        <b-card-text>
                            <small>Ebene:</small>
                            <b-form-select v-model="selectedLayer" :options="layers"></b-form-select>
                        </b-card-text>
                        <!--<b-card-text>
                            Schriftgröße | View auf Selektion eingrenzen (keine Selektion >
                            wieder alle anzeigen)
                        </b-card-text>-->
                    </b-card>
                    <b-card title="Elemente" class="p-3 mb-3">
                        <b-card-text v-if="getScopedElementIds.length == 1">
                            <b-row>
                                <b-button size="sm" variant="info" @click="showElementDetails(getScopedElementIds[0])">Element-Details</b-button>
                            </b-row>
                        </b-card-text>
                        <b-card-text>
                            <b-row>
                                <b-button size="sm" variant="outline-secondary" @click="addLayerElement(selectedLayer)">{{ selectedLayer }} hinzufügen</b-button>
                            </b-row>
                            <b-row class="mt-2">
                                <b-button size="sm" variant="outline-secondary" @click="addEmptyRequirement()">Requirement hinzufügen</b-button>
                            </b-row>
                        </b-card-text>
                        <!--<b-card-text>
                            <b-row class="mt-2">
                                <b-button size="sm" variant="info">Requirements verwalten</b-button>
                            </b-row>
                        </b-card-text>-->
                        <my-modal-selected-element
                             />
                    </b-card>
                    <the-overview-relations v-show="getScopedElementIds.length > 0"
                         />
                </b-col>
            </b-row>
        </b-container>
    </b-row>
</template>

<script>
import { mapMutations, mapGetters } from 'vuex'
import BasicSvgBubblePacking from './BasicSvgBubblePacking.vue'
import TheOverviewRelations from './TheOverviewRelations.vue'
import MyModalSelectedElement from './MyModalSelectedElement.vue'

export default {
  components: {
    BasicSvgBubblePacking,
    TheOverviewRelations,
    MyModalSelectedElement,
  },

  data() {
    return {
      graphWidth: 600,
      graphHeight: 600,
      selectedGrouping: "",
    };
  },

  created() {
    this.graphWidth = window.innerHeight - 150;
    this.graphHeight = window.innerHeight - 150;
    this.selectedGrouping = this.$viewLayers[0].name;
  },

    computed: {
        selectedLayer: {
            get() {
                return this.getSelectedLayer
            },
            set(newValue) {
                this.setSelectedLayer(newValue)
            }
        },
    
        ...mapGetters([
            'getHierarchiesByParentId',
            'getElementById',
            'getElementsByType',
            'getScopedConsumeNodes',
            'getScopedDependencyNodes',
            'getAllElements',
            'getScopedElementIds',
            'relationTypes',
            'layers',
            'getLastAddedElement',
            'getSelectedLayer'
        ]),
    // TODO: window resize

    graphNodes() {
        return this.generateSvgNodes();
    },
  },

  watch: {
    selectedGrouping() {
      this.resetScopedElements();
    },
  },

  methods: {
    getNestedLayerNodes(elemNodeId, targetType) {
        var targetNodes = this.getHierarchiesByParentId(elemNodeId)
            .map((e) => this.getElementById(e.childId));
        var returnNodes = [];
        for (var ne = 0; ne < targetNodes.length; ne++) {
            if (targetNodes[ne].type == targetType) {
            returnNodes.push({
                id: targetNodes[ne].id,
                name: targetNodes[ne].name,
            });
            } else {
            returnNodes = returnNodes.concat(
                this.getNestedLayerNodes(targetNodes[ne].id, targetType)
            );
            }
        }

        return returnNodes;
    },

    generateSvgNodes() {
      if (this.getAllElements == undefined) {
        return [];
      }

      var selectedLayer = this.selectedLayer;
      var svgNodes = [];
      var nestedNodes;

      switch (this.selectedLayer) {
        case this.$viewLayers[6].name: // Netzwerk (2-depth)
        case this.$viewLayers[5].name: // Applikation (2-depth)
        case this.$viewLayers[4].name: // Serviceinstanz (2-depth)
        case this.$viewLayers[3].name: // Solution (2-depth)
        case this.$viewLayers[2].name: // Release (2-depth)
        case this.$viewLayers[1].name: // Funktionalität (2-depth)
          var noProjectNode = {
            id: "ELMPRJ0",
            name: "No Project",
            parentNodeId: "ELM0",
          };

          var projectLayerElements = [];
          var addedProjects = [];
          this.getElementsByType(this.selectedGrouping)
            .forEach((elemProj) => {
              projectLayerElements = this.getNestedLayerNodes(
                elemProj.id,
                this.selectedLayer
              );
              if (projectLayerElements.length > 0) {
                svgNodes = svgNodes.concat(
                  projectLayerElements.map(function (rel) {
                    return {
                      id: rel.id,
                      name: rel.name,
                      parentNodeId: elemProj.id,
                    };
                  })
                );
                addedProjects.push(elemProj.id);
              }
            });

          addedProjects.forEach((elemProjId) => {
            var projName = this.getElementById(elemProjId).name;
            svgNodes.push({
              id: elemProjId,
              name: projName,
              parentNodeId: "ELM0",
            });
          });

          var noProjectLayerElements = this.getElementsByType(this.selectedLayer)
            .filter(function (elemRel) {
              return svgNodes.map((e) => e.id).indexOf(elemRel.id) == -1;
            });

          svgNodes = svgNodes.concat(
            noProjectLayerElements.map(function (elemRel) {
              return {
                id: elemRel.id,
                name: elemRel.name,
                parentNodeId: "ELMPRJ0",
              };
            })
          );

          svgNodes.push(noProjectNode);
          break;

        default:
        case this.$viewLayers[0].name: // Projekte (1-depth)
          svgNodes = this.getElementsByType(this.$viewLayers[0].name)
            .map(function (elemNode) {
              return {
                id: elemNode.id,
                name: elemNode.name,
                parentNodeId: "ELM0",
              };
            });
          break;
      }

      svgNodes = svgNodes.filter(
        (elem, ind, _arr) => _arr.map((e) => e.id).indexOf(elem.id) === ind
      );
      svgNodes.push({
        id: "ELM0",
        name: "",
        parentNodeId: "",
      });

      return d3
        .stratify()
        .id((n) => n.id)
        .parentId((n) => n.parentNodeId)(svgNodes);
    },

    showElementDetails(elementId) {
        this.setSelectedElementById(elementId)
        this.setShowElementModal(true)
    },

    addLayerElement(elementName) {
        this.addElement({
            name: "Neu " + elementName,
            type: elementName
        })
        var addedElement = this.getLastAddedElement
        this.showElementDetails(addedElement.id)
    },   

    addEmptyRequirement() {
        this.addElement({
            name: "Neues Requirement",
            type: "Requirement"
        })
        var addedElement = this.getLastAddedElement
        
        this.setSelectedRelationById("")
        this.setSelectedRequirementById(addedElement.id)
        this.setShowRelationModal(true)
    },

    ...mapMutations([
        'addElement',
        'setSelectedRelationById',
        'setSelectedRequirementById',
        'setShowRelationModal',
        'setShowElementModal',
        'setSelectedElementById',
        'addScopeElementId',
        'resetScopedElements',
        'setSelectedLayer'
    ])
  },
};
</script>

<style scoped>
</style>



