<template>
    <div class="row mt-2" v-if="getSelectedElement.hasFunctions">
        <div class="col">
            <div class="row">
                <div class="col">Funktionen</div>
            </div>
            <my-row-selected-element-function-details v-for="functionId in getSelectedElement.functions" :key="functionId"
                :functionId="functionId" />
            <div class="row mt-4">
                <div class="col-4"></div>
                <div class="col-7"><b-form-select v-model="selectedFunctionId" :options="ddFunctions"></b-form-select></div>
                <div class="col-1"><b-button class="mt-1" size="sm" variant="outline-dark" @click="linkFunction()"><b-icon-link></b-icon-link></b-button></div>
            </div>
            <div class="row mt-2">
                <div class="col-4"></div>
                <div class="col-7"><b-form-input v-model="newFunctionName" type="text" placeholder="Neue Funktion"></b-form-input></div>
                <div class="col-1"><b-button class="mt-1" size="sm" variant="outline-dark" @click="addFunction()"><b-icon-plus></b-icon-plus></b-button></div>
            </div>
        </div>
    </div>
</template>

<script>
import { mapGetters, mapMutations } from 'vuex'
import MyRowSelectedElementFunctionDetails from './MyRowSelectedElementFunctionDetails.vue'

export default {
    components: {
        MyRowSelectedElementFunctionDetails
    },

    data() {
       return {
            selectedFunctionId: "",
            newFunctionName: ""
        }
    },

    computed: {
        ddFunctions() {
            var functionElements = this.getddElementsGroupByType.find(el => el.label == 'Applikationsfunktion')
            if(functionElements != undefined) {
                return functionElements.options
            }
            return []
        },

        ...mapGetters([
            'getSelectedElement',
            'getddElementsGroupByType',
            'getLastAddedElement'
        ])
    },

    methods: {
        linkFunction() {
            if(this.selectedFunctionId.length > 0) {
                if(this.getSelectedElement.functions.indexOf(this.selectedFunctionId) == -1) {
                    var functionArray = this.getSelectedElement.functions.push(this.selectedFunctionId)
                    this.changeElementProp({
                        id: this.getSelectedElement.id,
                        prop: 'functions',
                        newValue: functionArray
                    })
                }
                this.selectedFunctionId = ""
            };
        },

        addFunction() {
            if(this.newFunctionName.length > 0) {
                this.addElement({
                    name: this.newFunctionName,
                    type: 'Funktion'
                })
                var addedElement = this.getLastAddedElement

                var functionArray = this.getSelectedElement.functions.push(addedElement.Id)
                this.changeElementProp({
                    id: this.getSelectedElement.id,
                    prop: 'functions',
                    newValue: functionArray
                })
                
                this.newFunctionName = ""
            }
        },

        ...mapMutations([
            'changeElementProp',
            'addElement'
        ])
    }
}
</script>

<style scoped>

</style>


