<template>
    <b-card no-body class="mb-1">
        <b-card-header header-tag="header" class="p-1" role="tab">
            <b-button block v-b-toggle.requirement-children variant="outline-dark">
                <div>Requirement :: Kinder <b-badge pill :class="{'bg-info': requirementChildren.length > 0 }">{{ (requirementChildren.length > 0 ? requirementChildren.length : "") }}</b-badge></div>
            </b-button>
        </b-card-header>
        <b-collapse id="requirement-children" accordion="modal-accordion" role="tabpanel">
        <b-card-body>
        <b-container fluid>
            <b-row class="mt-2 mb-2"><small><b-alert variant="info" show><u>Hinweis:</u> Änderungen werden nur für Realisierungen übernommen, welche auch die genannten Requirements realisieren</b-alert></small></b-row>
            <basic-row-add-link class="mb-2"
                :ddItems="possibleRequirements"
                buttonText="Requirement"
                :allowNewValue="false"
                @linkItem="linkChild" />
            <b-card no-body class="m-1 p-2" v-for="req in requirementChildren" :key="req.id">
                <b-row class="mt-2">
                    <b-col cols="3"><h6><span class="badge bg-primary text-white">{{ req.requirementTablet }}</span></h6></b-col>
                    <b-col cols="8"></b-col>
                    <b-col cols="1"><b-button size="sm" variant="outline-dark" @click="showRequirement(req.id)"><b-icon-gear></b-icon-gear></b-button></b-col>
                </b-row>
                <basic-row-input
                    rowLabel="Name"
                    :dataObject="req"
                    objectProp="name"
                    :isEditable="false" />
                <basic-row-textarea
                    rowLabel="Beschreibung"
                    :dataObject="req"
                    objectProp="description"
                    :isEditable="false" />
            </b-card>
        </b-container>
        </b-card-body>
        </b-collapse>
    </b-card>
</template>

<script>
import { mapGetters, mapMutations } from 'vuex'
import BasicRowTextarea from './BasicRowTextarea.vue'
import BasicRowInput from './BasicRowInput.vue'
import BasicRowAddLink from './BasicRowAddLink.vue'

export default {
    components: {
        BasicRowTextarea,
        BasicRowInput,
        BasicRowAddLink
    },

    computed: {
        requirementChildren() {
            var childIds = this.getHierarchiesByParentId(this.getSelectedRequirement.id).map(h => h.childId)
            return childIds.map(i => this.getElementById(i))
        },

        possibleRequirements() {
            var ddRequirements = []
            var nonRequirementIds = this.getChildrenOfSelectedRequirement.map(r => r.id)
            if(this.getParentOfSelectedRequirement != undefined) {
                nonRequirementIds.push(this.getParentOfSelectedRequirement.id)
            }

            this.getddRequirementsGroupByTablet.forEach((r) => {
                var labelOptions = {
                    label: r.label,
                    options: r.options.filter(ro => nonRequirementIds.indexOf(ro.value) == -1)
                }
                if(labelOptions.options.length > 0) {
                    ddRequirements.push(labelOptions)
                }
            })
            return ddRequirements
        },

        ...mapGetters([
            'getHierarchiesByParentId',
            'getSelectedRequirement',
            'getElementById',
            'getddRequirementsGroupByTablet',
            'getParentIdByChildId',
            'getChildrenOfSelectedRequirement',
            'getParentOfSelectedRequirement'
        ])
    },

    methods: {
        linkChild(childId) {
            this.addHierarchyObject({
                childId: childId,
                parentId: this.getSelectedRequirement.id
            })
        },

        showRequirement(elemId) {
            this.setSelectedRequirementById(elemId)
            this.setSelectedRelationById("")
        },

        ...mapMutations([
            'addHierarchyObject',
            'setSelectedRequirementById',
            'setSelectedRelationById'
        ])
    },
}
</script>

<style scoped>
    
</style>


