<template>
    <b-card no-body class="mb-1">
        <b-card-header header-tag="header" class="p-1" role="tab">
            <b-button block v-b-toggle.requirement-relations variant="outline-dark">
                <span>Requirement :: Beziehungen</span>
            </b-button>
        </b-card-header>
        <b-collapse id="requirement-relations" accordion="modal-accordion" role="tabpanel">
        <b-card-body>
        <b-container fluid>
            <b-row class="mt-2 mb-2"><small><b-alert variant="info" show><u>Hinweis:</u> Änderungen werden für alle Realisierungen übernommen</b-alert></small></b-row>
            <b-row class="mt-2">
                <b-col cols="2">Vorgänger:</b-col>
                <b-col cols="10">
                    <b-form-select v-model="predecessorModel" :options="ddRequirements" @change="changedPredecessor()"></b-form-select>
                </b-col>
            </b-row>
            <b-row class="mt-2">
                <b-col cols="2">Nachfolger:</b-col>
                <b-col cols="10">
                    <b-form-select v-model="successorModel" :options="ddRequirements" @change="changedSuccessor()"></b-form-select>
                </b-col>
            </b-row>
            <b-row class="mt-4 mb-2"><small><b-alert variant="info" show><u>Hinweis:</u> Änderungen werden nur für Realisierungen übernommen, welche auch die genannten Requirements realisieren</b-alert></small></b-row>
            <b-row class="mt-2">
                <b-col cols="2">Duplikate:</b-col>
                <b-col cols="10"><i>folgt ...</i></b-col>
            </b-row>
            <b-row class="mt-2">
                <b-col cols="2">Benötigt:</b-col>
                <b-col cols="10"><i>folgt ...</i></b-col>
            </b-row>
            <b-row class="mt-2">
                <b-col cols="2">Ähnliche:</b-col>
                <b-col cols="10"><i>folgt ...</i></b-col>
            </b-row>
            <!-- v-for: Duplicate(name, desc) -->
            <!-- v-for: Requires(name, desc) -->
            <!-- v-for: Similar(name, desc) -->
        </b-container>
        </b-card-body>
        </b-collapse>
    </b-card>
</template>

<script>
import { mapGetters, mapMutations } from 'vuex'
import BasicRowSelect from "./BasicRowSelect.vue"

export default {
    components: {
        BasicRowSelect
    },

    data() {
        return {
            predecessorModel: "",
            successorModel: ""
        }
    },

    mounted() {
        if(this.getPredecessorOfSelectedRequirement == undefined) {
                this.predecessorModel = ""            
        } else {
                this.predecessorModel = this.getPredecessorOfSelectedRequirement.id
        }

        if(this.getSuccessorOfSelectedRequirement == undefined) {
                this.successorModel = ""            
        } else {
                this.successorModel = this.getSuccessorOfSelectedRequirement.id
        }
    },

    computed: {
        ddRequirements() {
            var noOption = [{ 
                label: "keine Auswahl",
                options: ['']
            }]
            
            return noOption.concat(this.getddRequirementsGroupByTablet)
        },

        ...mapGetters([
            'getPredecessorOfSelectedRequirement',
            'getSuccessorOfSelectedRequirement',
            'getddRequirementsGroupByTablet',
            'getSelectedRequirement',
            'getLastAddedRelation',
            'getAllRelations'
        ])
    },

    methods: {
        changedPredecessor() {
            if(this.getPredecessorOfSelectedRequirement == undefined) {
                this.newReplaceRelation(this.predecessorModel, this.getSelectedRequirement.id)
            } else {
                this.changeReplaceRelation(
                    this.getPredecessorOfSelectedRequirement.id,
                    this.getSelectedRequirement.id,
                    this.predecessorModel, 'startId')
            }
        },

        changedSuccessor() {
            if(this.getSuccessorOfSelectedRequirement == undefined) {
                this.newReplaceRelation(this.getSelectedRequirement.id, this.successorModel)
            } else {
                this.changeReplaceRelation(
                    this.getSelectedRequirement.id,
                    this.getSuccessorOfSelectedRequirement.id,
                    this.successorModel, 'endId')
            }
        },

        newReplaceRelation(startId, endId) {
            this.addRelation({
                startId: startId,
                endId: endId,
                layer: 'Requirement'
            })
            var newRelation = this.getLastAddedRelation
            this.changeRelationProp({
                id: newRelation.id,
                prop: 'type',
                newValue: 'replace'
            })
        },

        changeReplaceRelation(startId, endId, newVal, propName) {
            var successorRelation = this.getAllRelations.find(rel =>
                rel.endId == endId &&
                rel.startId == startId &&
                rel.type == 'replace') 
            if(newVal.length > 0) {
                this.changeRelationProp({
                    id: successorRelation.id,
                    prop: propName,
                    newValue: newVal
                })
            } else { // delete
                this.deleteRelationById(successorRelation.id)
            }
        },

        ...mapMutations([
            'addRelation',
            'changeRelationProp',
            'deleteRelationById'
        ])
    }
}
</script>

<style scoped>

</style>


