<template>
    <b-row>
        <b-col cols="5">
            <basic-row-add-link class="w-50"
                :ddItems="getddRequirementsGroupByTablet"
                buttonText="Requirement"
                @addItem="addRequirement"
                @linkItem="linkRequirement" />
        </b-col>
        <b-col cols="1"><b-form-checkbox v-model="onlyScopedInternal" v-show="getScopedElementIds.length > 0">
            nur diese Auswahl (Start-Ende-Element)
        </b-form-checkbox>
        <b-form-checkbox v-model="withElementInternal" v-show="getScopedElementIds.length > 0">
            Element-intern
        </b-form-checkbox></b-col>
        <b-col cols="4">
            <b-form-group class="mt-2">
                <b-form-tags v-model="filterValues" no-outer-focus class="mb-2">
                    <template v-slot="{ tags, disabled, addTag, removeTag }">
                        <ul v-if="tags.length > 0" class="list-inline d-inline-block mb-2">
                            <li v-for="tag in tags" :key="tag" class="list-inline-item">
                                <b-form-tag
                                    @remove="removeFilterTag({tag, removeTag})"
                                    :title="tag"
                                    :disabled="disabled"
                                    variant="info">{{ tag }}</b-form-tag>
                            </li>
                        </ul>

                    <b-dropdown size="sm" variant="outline-secondary" block menu-class="w-100">
                        <template #button-content>
                            <b-icon-tag-fill></b-icon-tag-fill> Filter
                        </template>
                        <b-dropdown-form @submit.stop.prevent="() => {}">
                            <b-form-group
                                label="Tags durchsuchen"
                                label-for="tag-search-input"
                                label-cols-md="auto"
                                class="mb-0"
                                label-size="sm"
                                :description="searchDesc"
                                :disabled="disabled">
                                    <b-form-input
                                        v-model="tagSearch"
                                        id="tag-search-input"
                                        type="search"
                                        size="sm"
                                        autocomplete="off"></b-form-input>
                            </b-form-group>
                        </b-dropdown-form>
                        <b-dropdown-divider></b-dropdown-divider>
                        <b-dropdown-item-button
                            v-for="option in availableOptions"
                            :key="option"
                            @click="filterRequirementTable({ option, addTag })">
                        {{ option }}
                        </b-dropdown-item-button>
                        <b-dropdown-text v-if="availableOptions.length == 0">keine Filteroptionen</b-dropdown-text>
                    </b-dropdown>
                    </template>
                </b-form-tags>
                </b-form-group>
        </b-col>
        <b-col cols="2" class="text-right align-text-bottom">{{ "zeigt " + this.getTabletRequirementsCount + " von " + getAllRequirements.length}}</b-col>
    </b-row>
</template>

<script>
import { mapGetters, mapMutations } from 'vuex'
import BasicRowAddLink from './BasicRowAddLink.vue'

export default {

    components: {
        BasicRowAddLink
    },

    data() {
        return {
            onlySelectedProjects: false,
            withInternal: true,
            filterValues: [],
            tagSearch: "",
            optionValueSeparator: "::"
        }
    },

    computed: {

        ddTagOptions() {
            var options = []
            options.push("Tag" + this.optionValueSeparator + "hat Eltern")
            options.push("Tag" + this.optionValueSeparator + "hat Kinder")
            options.push("Tag" + this.optionValueSeparator + "nicht verwendet")
            options.push("Tag" + this.optionValueSeparator + "hat Vorgänger")
            options.push("Tag" + this.optionValueSeparator + "hat Nachfolger")

            this.getLabels.forEach((l) => {
                options.push("Label" + this.optionValueSeparator + l)
            })
            options.push("Label" + this.optionValueSeparator + "NOTSET")

            this.tabletTypes.forEach((type) => {
                type.options.forEach((to) => {
                    options.push("Tablet" + this.optionValueSeparator + to)
                })
            })
            options.push("Tablet" + this.optionValueSeparator + "NOTSET")

            this.statusTypes.forEach((s) => {
                options.push("Status" + this.optionValueSeparator + s)
            })
            options.push("Status" + this.optionValueSeparator + "NOTSET")

            this.getddElementsGroupByType.filter(el => this.layers.indexOf(el.label) != -1)
                .forEach((type) => {
                    type.options.forEach((to) => {
                        options.push(
                            type.label + this.optionValueSeparator + 
                            to.text + this.optionValueSeparator + 
                            to.value)
                    })
                })

            return options
        },

        onlyScopedInternal: {
            get() {
                return this.getScopedArgumentOnlyScopedInternal
            },
            set(newValue) {
                this.setScopedArgumentOnlyScopedInternal(newValue)
            }
        },

        withElementInternal: {
            get() {
                return this.getScopedArgumentWithElementInternal
            },
            set(newValue) {
                this.setScopedArgumentWithElementInternal(newValue)
            }
        },

        searchCriteria() {
            return this.tagSearch.trim().toLowerCase()
        },

        searchDesc() {
            if (this.searchCriteria && this.availableOptions.length === 0) {
                return 'kein Treffer'
            }
            return ''
        },

        availableOptions() {
            const criteria = this.searchCriteria
            const options = this.ddTagOptions.filter(opt => this.filterValues.indexOf(opt) == -1)
            
            if (criteria) {
                return options.filter(opt => opt.toLowerCase().indexOf(criteria) > -1);
            }
            return options
        },

        ...mapGetters([
            'getddRequirementsGroupByTablet',
            'getScopedElementIds',
            'getddElementsGroupByType',
            'getAllRequirements',
            'getScopedArgumentOnlyScopedInternal',
            'getScopedArgumentWithElementInternal',
            'getLastAddedRelation',
            'getLastAddedElement',
            'getElementById',
            'getLabels',
            'tabletTypes',
            'statusTypes',
            'layers',
            'getTabletRequirementsCount'
        ])
    },

    methods: {
        removeFilterTag({tag, removeTag}) {
            removeTag(tag)
            
            this.setFilterValues(this.filterValues.filter(fv => fv != tag))
        },

        filterRequirementTable({option, addTag}) {
            addTag(option)
            this.tagSearch = ""

            this.setFilterValues(this.filterValues.concat(option))
        },

        setFilterValues(values) {
            var filterTags = []
            values.forEach((t) => {
                var tagString = t.split(this.optionValueSeparator)
                filterTags.push({
                    type: tagString[0],
                    name: tagString[1],
                    id: (tagString[2] != undefined ? tagString[2] : "")
                })
            })

            this.$emit('changeFilter', filterTags)
        },

        addRequirement(elementName) {
            this.addElement({
                name: elementName,
                type: 'Requirement'
            })

            var newRequirement = this.getLastAddedElement
            
            if(this.getScopedElementIds.length > 0) {
                this.changeElementProp({
                    id: newRequirement.id,
                    prop: 'requirementSource',
                    newValue: this.getScopedElementIds[0]
                })

                this.linkRequirement(newRequirement.id)
            } else {
                this.setSelectedRelationById("")
                this.setSelectedRequirementById(newRequirement.id)
                this.setShowRelationModal(true)
            }
        },

        linkRequirement(elementId) {
            var relationId = ""
            if(this.getScopedElementIds.length > 0) {
                var startRelationId = this.getScopedElementIds[0]
                if(this.getScopedElementIds[1] != undefined) {
                    var endRelationId = this.getScopedElementIds[1]
                    var relationLayer = this.getElementById(endRelationId).type
                } else {
                    var endRelationId = ""
                    var relationLayer = ""
                }

                this.addRelation({
                    startId: startRelationId,
                    endId: endRelationId,
                    layer: relationLayer,
                    requirementId: elementId
                })
                relationId = this.getLastAddedRelation.id
            }
            this.setSelectedRelationById(relationId)
            this.setSelectedRequirementById(elementId)
            this.setShowRelationModal(true)
        },

        ...mapMutations([
            'setScopedArgumentWithElementInternal',
            'setScopedArgumentOnlyScopedInternal',
            'setSelectedRelationById',
            'setSelectedRequirementById',
            'setShowRelationModal',
            'addRelation',
            'addElement',
            'changeElementProp'
        ])
    }
    
}
</script>

<style scoped>

</style>


