<template>
    <b-row class="mt-2">
        <div v-show="allowNewValue || (showAddItem || showLinkItem)">
            <b-button v-show="allowNewValue && (!showAddItem && !showLinkItem)" variant="outline-dark" @click="showAddItem = !showAddItem"><b-icon-plus></b-icon-plus> {{ buttonText }}</b-button>
            <b-button v-show="showAddItem || showLinkItem" variant="outline-dark" @click="showAddItem = false; showLinkItem = false"><b-icon-arrow-left></b-icon-arrow-left></b-button>
        </div>
        <div class="m-1" v-if="ddLabel.length == 0">
            <b-button v-show="!showLinkItem && !showAddItem" variant="outline-dark" @click="showLinkItem = !showLinkItem"><b-icon-link></b-icon-link> {{ buttonText }}</b-button>
            <b-form-select v-show="showLinkItem" v-model="linkedItem" :options="ddLabelItems"></b-form-select>
            <b-form-input v-show="showAddItem" type="text" v-model="newItem"></b-form-input>
        </div>
        <div class="m-1" v-if="ddLabel.length > 0">
            <b-button v-show="!showLinkItem && !showAddItem" variant="outline-dark" @click="showLinkItem = !showLinkItem"><b-icon-link></b-icon-link> {{ buttonText }}</b-button>
            <b-form-select v-show="showLinkItem" v-model="labelItem" :options="ddLabel"></b-form-select>
            <b-form-input v-show="showAddItem" type="text" v-model="newItem"></b-form-input>
        </div>
        <div class="m-1" v-if="ddLabel.length > 0">
            <b-form-select v-show="showLinkItem" v-model="linkedItem" :options="ddLabelItems"></b-form-select>
        </div>
        <div class="m-1">
            <b-button v-show="showAddItem" variant="outline-dark" @click="addItem()"><b-icon-plus></b-icon-plus></b-button>
            <b-button v-show="showLinkItem" variant="outline-dark" @click="linkItem()"><b-icon-link></b-icon-link></b-button>
        </div>
    </b-row>
</template>

<script>
export default {
    props: {
        ddItems: {
            type: Array,
            required: true
        },
        buttonText: {
            type: String,
            required: false,
            default: 'Item'
        },
        allowNewValue: {
            type: Boolean,
            required: false,
            default: () => true
        }
    },

    data() {
        return {
            showAddItem: false,
            newItem: "",
            showLinkItem: false,
            linkedItem: "",
            labelItem: ""
        }
    },

    computed: {
        ddLabel() {
            if(this.ddItems.length > 0) {
                if(this.ddItems[0].label != undefined) {
                    var ddLabelItems = this.ddItems.map(i => i.label);
                    this.labelItem = ddLabelItems[0];
                    return ddLabelItems;
                }
            }
            return [''];
        },

        ddLabelItems() {
            if(this.ddLabel.length > 0) {
                var labelItems = this.ddItems.find(i => i.label == this.labelItem);
                if(labelItems != undefined) {
                    return labelItems.options;
                }
            };
            return this.ddItems;
        }
    },

    methods: {
        addItem() {
            this.$emit('addItem', this.newItem);
            this.showAddItem = false;
            this.newItem = "";
        },

        linkItem() {
            this.$emit('linkItem', this.linkedItem);
            this.showLinkItem = false;
        },

        
    }
}
</script>

<style scoped>
div {
    display: inline-block;
    }
</style>


