<template>
    <b-row class="mt-2">
        <b-col cols="1">Start:</b-col>
        <b-col cols="5"><b-form-input type="date" v-model="valueStart" @blur="changeElement(startProp, valueStart)" :disabled="!isEditable"></b-form-input></b-col>
        <b-col cols="1">Ende:</b-col>
        <b-col cols="5"><b-form-input type="date" v-model="valueEnd" @blur="changeElement(endProp, valueEnd)" :disabled="!isEditable"></b-form-input></b-col>
    </b-row>
</template>

<script>
export default {
    props: {
        startProp: {
            type: String,
            required: false,
            default: "startDate"
        },
        endProp: {
            type: String,
            required: false,
            default: "endDate"
        },
        dataObject: {
            type: Object,
            required: true
        },
        isEditable: {
            type: Boolean,
            required: false,
            default: true
        }
    },

    data() {
        return {
            valueStart: this.dataObject[this.startProp],
            valueEnd: this.dataObject[this.endProp]
        }
    },

    methods: {
        changeElement(elemProp, newElemValue) {
            this.$emit('changeElement', this.dataObject.id, elemProp, newElemValue);
        },
    }
}
</script>

<style scoped>

</style>


