<template>
    <div>
        <h4 @click="showEdit = true"><b-badge v-show="!showEdit" variant="info">{{ (selectedOption.length > 0 ? selectedOption : "...") }}</b-badge></h4>
        <b-button v-show="showEdit" class="m-1" size="sm" variant="outline-dark" @click="showEdit = false"><b-icon-arrow-left></b-icon-arrow-left></b-button>
        <b-form-select v-show="showEdit" v-model="selectedOption" :options="options" @change="changeItem"></b-form-select>
    </div>
</template>

<script>
export default {
    props: {
        options: {
            type: Array,
            required: true
        },
        selected: {
            type: String,
            required: true
        },
        dataObjectId: {
            type: String,
            required: true
        }
    },

    data() {
        return {
            showEdit: false,
            selectedOption: this.selected
        }
    },

    methods: {
        changeItem() {
            this.$emit('changeItem', this.dataObjectId, this.selectedOption);
            this.showEdit = false;
        }
    }
}
</script>

<style scoped>

</style>


