import methodsGeneral from '../../js/methodsGeneral.js'

function setRelationTypeValues(rel) {
    var relationObject = {
        id: rel.id,
        startId: rel.startId,
        endId: rel.endId,
        layer: rel.layer,
    }

    relationObject.timestamp = methodsGeneral.getJSONObjectValueByName(rel, 'timestamp')
    relationObject.isRealization = false

    if (relationObject.layer != 'Requirement') {
        relationObject.comment = methodsGeneral.getJSONObjectValueByName(rel, 'comment')
        relationObject.status = methodsGeneral.getJSONObjectValueByName(rel, 'status')
        relationObject.jiralink = methodsGeneral.getJSONObjectValueByName(rel, 'jiralink')

        if (methodsGeneral.getJSONObjectValueByName(rel, 'element').length > 0) {
            relationObject.isRealization = true
            relationObject.requirementId = methodsGeneral.getJSONObjectValueByName(rel, 'element')
        } else if (rel.requirementId != undefined && rel.requirementId.length > 0) {
            relationObject.isRealization = true
            relationObject.requirementId = rel.requirementId
        }

        if (relationObject.isRealization) {
            relationObject.solved = methodsGeneral.getJSONObjectValueByName(rel, 'solved')
            relationObject.requirementEnddate = methodsGeneral.getJSONObjectValueByName(rel, 'enddate')
            relationObject.commitment = methodsGeneral.getJSONObjectValueByName(rel, 'commitment')

        } else { // architecture relation
            relationObject.title = methodsGeneral.getJSONObjectValueByName(rel, 'title')
            relationObject.description = methodsGeneral.getJSONObjectValueByName(rel, 'description')
            relationObject.tablet = methodsGeneral.getJSONObjectValueByName(rel, 'tablet')
        }
    } else {
        relationObject.type = methodsGeneral.getJSONObjectValueByName(rel, 'type')
    }
    return relationObject
}

function reverseRelationTypeValues(rel) {
    var relationObject = {
        id: rel.id,
        startId: rel.startId,
        endId: rel.endId,
        layer: rel.layer
    }
    relationObject.values = []

    relationObject.values.push({
        name: 'timestamp',
        value: rel.timestamp
    })

    if (rel.layer == 'Requirement') {
        relationObject.values.push({
            name: 'type',
            value: rel.type
        })
    } else {
        relationObject.values.push({
            name: 'comment',
            value: rel.comment
        })
        relationObject.values.push({
            name: 'status',
            value: rel.status
        })
        relationObject.values.push({
            name: 'jiralink',
            value: rel.jiralink
        })
    }

    if (rel.isRealization) { // new realization
        relationObject.values.push({
            name: 'element',
            value: rel.requirementId
        })
        relationObject.values.push({
            name: 'solved',
            value: rel.solved
        })
        relationObject.values.push({
            name: 'enddate',
            value: rel.requirementEnddate
        })
        relationObject.values.push({
            name: 'commitment',
            value: rel.commitment
        })

    } else if (rel.layer != 'Requirement') { // architecture relation
        relationObject.values.push({
            name: 'title',
            value: rel.title
        })
        relationObject.values.push({
            name: 'description',
            value: rel.description
        })
        relationObject.values.push({
            name: 'tablet',
            value: rel.tablet
        })
    }

    return relationObject
}

function upScaleNodes(rootGetters, nodeArray) { // [value, depth]
    var scaleNodes = [];
    var nodeArrayCount = nodeArray.length;
    for (var e = 0; e < nodeArrayCount; e++) {
        var nodeElement = nodeArray[e];
        var currentDepth = nodeElement.depth;
        var currentId = nodeElement.value;
        var parentId = nodeElement.value;

        while (currentDepth > 0) {
            parentId = rootGetters.getParentIdByChildId(currentId)
            if (parentId.length > 0) {
                currentId = parentId;
                currentDepth--;
            } else {
                currentDepth = 0;
            }
        }
        if (scaleNodes.indexOf(currentId) == -1) {
            scaleNodes.push(currentId);
        }
    }

    return scaleNodes;
}

function getRandomRelationId(relationStorage) {
    var randomNumber;
    do {
        randomNumber = Math.floor((Math.random() * 1000000) + 1);
    } while (relationStorage.map(r => r.id).indexOf("CON" + randomNumber) != -1);

    return "CON" + randomNumber;
};

export default {
    getters: {
        getRelationById: (state) => (relationId) => {
            return state.relations.find(rel => rel.id == relationId)
        },
        getReverseRelation: (state) => (relationId) => {
            return reverseRelationTypeValues(relationId)
        },
        getSelectedRelationIdHistory(state) {
            return state.selectedRelationIdHistory
        },
        getScopedConsumeRelations(state, getters, rootState, rootGetters) {
            var consumerRelations = []

            if (rootGetters.getScopedElementIds.length == 1) {
                var selectedElementIdValues = rootGetters.getScopedElementsWithNestedIds.map(e => e.value)
                consumerRelations = state.relations.filter(rel => selectedElementIdValues.indexOf(rel.startId) != -1)
            };
            return consumerRelations
        },

        getScopedConsumeNodes(state, getters, rootState, rootGetters) {
            if (rootGetters.getScopedElementIds.length == 1) {
                var nestedIdValues = rootGetters.getScopedElementsWithNestedIds.map(e => e.value)
                var consumeElements = getters.getScopedConsumeRelations.map((rel) => {
                    var startNode = rootGetters.getScopedElementsWithNestedIds[nestedIdValues.indexOf(rel.startId)]
                    return {
                        value: rel.endId,
                        depth: startNode.depth,
                    }
                })

                return upScaleNodes(rootGetters, consumeElements)
            }

            return []
        },

        getScopedDependencyRelations(state, getters, rootState, rootGetters) {
            var dependencyRelations = []

            if (rootGetters.getScopedElementIds.length < 2) {
                var selectedElementIdValues = rootGetters.getScopedElementsWithNestedIds.map(r => r.value)
                dependencyRelations = state.relations.filter(rel => selectedElementIdValues.indexOf(rel.endId) != -1)

            } else if (rootGetters.getScopedElementsWithNestedIds.length > 1) {
                var selectedElementIdValues = rootGetters.getScopedElementsWithNestedIds.map(r => r.value)
                dependencyRelations = state.relations.filter(rel => {
                    var primaryIndex = selectedElementIdValues.indexOf(rel.endId)
                    var secondaryIndex = selectedElementIdValues.indexOf(rel.startId)

                    if (primaryIndex != -1 && secondaryIndex != -1) {
                        if (rootGetters.getScopedElementsWithNestedIds[primaryIndex].parent != rootGetters.getScopedElementsWithNestedIds[secondaryIndex].parent) {
                            return true
                        } else {
                            return false
                        }
                    }
                    return false
                })
            }
            return dependencyRelations
        },

        getScopedRelations(state, getters, rootState, rootGetters) {
            if (rootGetters.getScopedElementIds.length == 1) {
                var selectedElementIdValues = rootGetters.getScopedElementsWithNestedIds.map(e => e.value)
                return state.relations.filter(rel => selectedElementIdValues.indexOf(rel.startId) != -1 || selectedElementIdValues.indexOf(rel.endId) != -1)
            } else if (rootGetters.getScopedElementIds.length > 1) {
                return getters.getScopedDependencyRelations
            }
            return []
        },

        getScopedDependencyNodes(state, getters, rootState, rootGetters) {
            var dependencyElements = getters.getScopedDependencyRelations.map((rel) => {
                var nestedIdValues = rootGetters.getScopedElementsWithNestedIds.map(e => e.value)
                var endNode = rootGetters.getScopedElementsWithNestedIds[nestedIdValues.indexOf(rel.endId)]
                return {
                    value: rel.startId,
                    depth: (endNode != undefined ? endNode.depth : -1),
                }
            })

            return upScaleNodes(rootGetters, dependencyElements)
        },

        getScopedConsumeRelationsSumTablet(state, getters, rootState, rootGetters) {
            var sumArray = []
                // @ignore architecture relations
            var requirementIds = []
            getters.getScopedConsumeRelations.forEach((rel) => {
                if (rel.isRealization) {
                    requirementIds.push(rootGetters.getElementById(rel.requirementId))
                }
            })
            var requirementsWithTablet = requirementIds.filter(req => req.requirementTablet.length > 0)

            rootGetters.tabletOptions.forEach((tabletElement, ind) => {
                sumArray[ind] = requirementsWithTablet.filter((elem) => {
                    if (elem.requirementTablet == tabletElement) {
                        return true
                    } else {
                        return false
                    }
                }).length
            })
            return sumArray
        },

        getScopedDependencyRelationsSumTablet(state, getters, rootState, rootGetters) {
            var sumArray = [];
            // @ignore architecture relations
            var requirementIds = [];
            getters.getScopedDependencyRelations.forEach((rel) => {
                if (rel.isRealization) {
                    requirementIds.push(rootGetters.getElementById(rel.requirementId))
                }
            })
            var requirementsWithTablet = requirementIds.filter(req => req.requirementTablet.length > 0)

            rootGetters.tabletOptions.forEach((tabletElement, ind) => {
                sumArray[ind] = requirementsWithTablet.filter((elem) => {
                    if (elem.requirementTablet == tabletElement) {
                        return true
                    } else {
                        return false
                    }
                }).length
            })
            return sumArray
        },

        getScopedConsumeRelationSumLayer(state, getters, rootState, rootGetters) {
            var sumArray = []
            rootGetters.layers.forEach((lay, ind) => {
                sumArray[ind] = getters.getScopedConsumeRelations.filter(r => r.layer == lay).length
            })
            return sumArray
        },

        getScopedDependencyRelationSumLayer(state, getters, rootState, rootGetters) {
            var sumArray = []
            rootGetters.layers.forEach((lay, ind) => {
                sumArray[ind] = getters.getScopedDependencyRelations.filter(r => r.layer == lay).length
            })
            return sumArray
        },

        getRealizationsOfSelectedRequirement(state, getters, rootState, rootGetters) {
            return state.relations.filter(r => r.isRealization && r.requirementId == rootGetters.getSelectedRequirement.id)
        },

        getPredecessorOfSelectedRequirement(state, getters, rootState, rootGetters) {
            return getters.getPredecessorOfRequirement(rootGetters.getSelectedRequirement.id)
        },

        getSuccessorOfSelectedRequirement(state, getters, rootState, rootGetters) {
            return getters.getSuccessorOfRequirement(rootGetters.getSelectedRequirement.id)
        },

        getPredecessorOfRequirement: (state, getters, rootState, rootGetters) => (requirementId) => {
            var requirementRelations = state.relations.filter(rel => rel.endId == requirementId)
            var replacedElement
            requirementRelations.forEach((rel) => {
                if (rel.type == "replace") {
                    replacedElement = rootGetters.getElementById(rel.startId)
                }
            })
            return replacedElement
        },

        getSuccessorOfRequirement: (state, getters, rootState, rootGetters) => (requirementId) => {
            var requirementRelations = state.relations.filter(rel => rel.startId == requirementId)
            var replacedElement
            requirementRelations.forEach((rel) => {
                if (rel.type == "replace") {
                    replacedElement = rootGetters.getElementById(rel.endId)
                }
            })
            return replacedElement
        },

        getRealizationsOfRequirement: (state) => (requirementId) => {
            return state.relations.filter(r => r.isRealization && r.requirementId == requirementId)
        },

        getddRelationsGroupByLayer(state, getters, rootState, rootGetters) {
            var ddRelations = state.relations
                .filter(rel => !rel.isRealization)
                .map((rel) => {
                    if (rel.title.length == 0) {
                        var relationText = "title not set"
                    } else {
                        var relationText = rel.title
                    }

                    return {
                        value: rel.id,
                        text: relationText + " (" + rel.startId + " > " + rel.endId + ")",
                        layer: rel.layer
                    }
                })
            var groupedRelations = d3.nest()
                .key(function(e) { return e.layer })
                .entries(ddRelations);
            groupedRelations = groupedRelations.map((rel) => ({
                label: rel.key,
                options: rel.values.sort((a, b) => (a.text < b.text ? -1 : 1))
            }))
            return groupedRelations;
        },

        getSelectedRelation(state) {
            return state.selectedRelation
        },

        getLastAddedRelation(state) {
            var lastRelation = state.relations.find(r => r.id == state.lastAddedRelationId)
            return lastRelation
        },

        getAllRelations(state) {
            return state.relations
        },

        getAllRealizations(state) {
            return state.relations.filter(r => r.isRealization)
        }
    },

    mutations: {
        replaceRelations(state, rels) {
            state.relations = rels.map(rel => setRelationTypeValues(rel))
        },

        deleteRelationById(state, relId) {
            state.relations = state.relations.filter(r => r.id != relId)
        },

        setSelectedRelationById(state, relId) {
            if (state.selectedRelation.id != undefined) {
                state.selectedRelationIdHistory.push(state.selectedRelation.id)
            }
            if (relId.length > 0) {
                state.selectedRelation = state.relations.find(r => r.id == relId)
            } else {
                state.selectedRelation = {}
            }
        },

        selectedLastRelation(state) {
            if (state.selectedRelationIdHistory.length > 0) {
                var lastRelationId = state.selectedRelationIdHistory.pop()
                state.selectedRelation = state.relations.find(e => e.id == lastRelationId)
            }
        },

        resetSelectedRelationIdHistory(state) {
            state.selectedRelationIdHistory = []
        },

        addRelation(state, relationObject) {
            state.lastAddedRelationId = getRandomRelationId(state.relations)
            var newRelation = setRelationTypeValues({
                id: state.lastAddedRelationId,
                startId: relationObject.startId,
                endId: relationObject.endId,
                layer: relationObject.layer,
                requirementId: (relationObject.requirementId == undefined ? "" : relationObject.requirementId),
                timestamp: methodsGeneral.getCurrentDate()
            })
            state.relations.push(newRelation)
        },

        changeRelationProp(state, relationObject) {
            var changedRelation = state.relations.find(e => e.id == relationObject.id)
            changedRelation[relationObject.prop] = relationObject.newValue
            changedRelation.timestamp = methodsGeneral.getCurrentDate()
        },
    },

    state: () => ({
        relations: [],
        selectedRelation: {},
        selectedRelationIdHistory: [],
        lastAddedRelationId: ""
    }),
}


