<template>
    <b-container id="app" fluid>
        <the-section-storage
             />
        <the-section-architecture v-show="isViewModeRelation"
             />
        <the-section-relations v-show="isViewModeRelation"
             />
        <the-section-requirements v-if="isViewModeRequirement"
             />
    </b-container>
</template>

<script>
import { mapGetters, mapMutations } from 'vuex'
import TheSectionRelations from './components/TheSectionRelations.vue'
import TheSectionStorage from './components/TheSectionStorage.vue'
import TheSectionRequirements from './components/TheSectionRequirements.vue'
import TheSectionArchitecture from './components/TheSectionArchitecture.vue'

export default {
    name: 'App',

    components: {
        TheSectionRelations,
        TheSectionStorage,
        TheSectionRequirements,
        TheSectionArchitecture
    },

    watch: {
        getSelectedLayer(newValue) {
            this.resetScopedElements()
        }
    },

    computed: {
        ...mapGetters([
            'isViewModeRelation',
            'isViewModeRequirement',
            'getSelectedLayer'
        ]),
    },

    methods: {
        ...mapMutations([
            'resetScopedElements'
        ])
    }
}
</script>

<style>

</style>


