<template>
    <b-container class="ml-2" fluid>
        <the-requirement-filter
            @changeFilter="setCriteria" />
        <the-requirement-table
            :tableObjects="filteredRequirements" />
        <my-modal-selected-relation
             />
    </b-container>
</template>

<script>
import TheRequirementFilter from './TheRequirementFilter.vue'
import TheRequirementTable from './TheRequirementTable.vue'
import MyModalSelectedRelation from './MyModalSelectedRelation.vue'
import { mapGetters, mapMutations } from 'vuex'

export default {
    components: {
        TheRequirementFilter,
        TheRequirementTable,
        MyModalSelectedRelation
    },

    data() {
        return {
            filterCriteria: []
        }
    },

    computed: {
        filteredRequirements() {
            var displayedRequirements = []
            this.resetScopedElements()

            var elementTypes = this.getddElementsGroupByType.map(t => t.label)
            var elementScopeCriteria = this.filterCriteria.filter(t => elementTypes.indexOf(t.type) != -1)
            if(elementScopeCriteria.length > 0) {
                elementScopeCriteria.forEach((type) => {
                    this.addScopeElementId(type.id)
                })
                displayedRequirements = this.getScopedRequirements
            } else if(this.filterCriteria.length > 0) {
                displayedRequirements = this.getAllRequirements
            }
            

            var tabletCriteria = this.filterCriteria.filter(c => c.type == 'Tablet')
            if(tabletCriteria.length > 0) { // requirement tablet in scopedElements
                var filterTablet = tabletCriteria.map(tc => tc.name)
            }

            var labelCriteria = this.filterCriteria.filter(c => c.type == 'Label')
            if(labelCriteria.length > 0) {
                var filterLabel = labelCriteria.map(lc => lc.name)
            }

            var statusCriteria = this.filterCriteria.filter(c => c.type == 'Status')
            if(statusCriteria.length > 0) {
                var filterStatus = statusCriteria.map(lc => lc.name)
            }

            var tagCriteria = this.filterCriteria.filter(c => c.type == 'Tag')
            if(tagCriteria.length > 0) {
                var filterTag = tagCriteria.map(lc => lc.name)
            }

            displayedRequirements = displayedRequirements.filter((req) => {
                var inScope = true
                var requirementRealizations = this.getRealizationsOfRequirement(req.id)
                
                if(filterTablet != undefined) {
                    inScope = false
                    if(filterTablet.indexOf("NOTSET") != -1 && req.requirementTablet.length == 0) {
                        inScope = true
                    }
                    if(filterTablet.indexOf(req.requirementTablet) != -1) {
                        inScope = true
                    }
                } 
                
                if(filterLabel != undefined) {
                    inScope = false
                    if(filterLabel.indexOf("NOTSET") != -1 && req.requirementLabels.length == 0) {
                        inScope = true
                    }
                    req.requirementLabels.forEach((rl) => {
                        if(filterLabel.indexOf(rl) != -1) {
                            inScope = true
                        }
                    })
                }

                if(filterStatus != undefined) {
                    inScope = false
                    requirementRealizations.forEach((rel) => {
                        if(filterStatus.indexOf("NOTSET") != -1 && rel.status.length == 0) {
                            inScope = true
                        }
                        if(filterStatus.indexOf(rel.status) != -1) {
                            inScope = true
                        }
                    })
                    
                }

                if(filterTag != undefined) {
                    inScope = false
                    if(filterTag.indexOf("hat Kinder") != -1) {
                        if(this.getHierarchiesByParentId(req.id).length > 0) {
                            inScope = true
                        }
                    }
                    if(filterTag.indexOf("hat Eltern") != -1) {
                        if(this.getParentIdByChildId(req.id) != "") {
                            inScope = true
                        }
                    }

                    if(filterTag.indexOf("nicht verwendet") != -1) {
                        if(requirementRealizations.length == 0) {
                            inScope = true
                        }
                    }

                    if(filterTag.indexOf("hat Vorgänger") != -1) {
                        if(this.getPredecessorOfRequirement(req.id) != undefined) {
                            inScope = true
                        }
                    }

                    if(filterTag.indexOf("hat Nachfolger") != -1) {
                        if(this.getSuccessorOfRequirement(req.id) != undefined) {
                            inScope = true
                        }
                    }
                }
                return inScope
            })

            this.setTabletRequirementsCount(displayedRequirements.length)
            return displayedRequirements
        },

        ...mapGetters([
            'getScopedRequirements',
            'getAllRequirements',
            'getddElementsGroupByType',
            'getHierarchiesByParentId',
            'getParentIdByChildId',
            'getRealizationsOfRequirement',
            'getPredecessorOfRequirement',
            'getSuccessorOfRequirement'
        ])
    },

    methods: {
        setCriteria(newValue) {
            this.filterCriteria = newValue
        },

        ...mapMutations([
            'resetScopedElements',
            'addScopeElementId',
            'setTabletRequirementsCount'
        ])
    }
}
</script>

<style scoped>

</style>


