<template>
    <b-row class="mt-2">
        <b-col cols="2">Id:</b-col>
        <b-col cols="2"><b-form-input type="text" v-model="dataObject.id" disabled></b-form-input></b-col>
        <b-col cols="1">Name:</b-col>
        <b-col cols="7"><b-form-input type="text" v-model="dataObject.name" @blur="changeElement('name', dataObject.name)" :disabled="!isEditable"></b-form-input></b-col>
    </b-row>    
</template>

<script>
export default {
    props: {
        dataObject: {
            type: Object,
            required: true
        },
        isEditable: {
            type: Boolean,
            required: false,
            default: true
        },
    },

    methods: {
        changeElement(elemProp, newElemValue) {
            this.$emit('changeElement', this.dataObject.id, elemProp, newElemValue);
        }
    }
}
</script>

<style scoped>

</style>


