export default {
    getters: {
        getViewModeArchitecture(state) {
            return state.viewModeArchitecture
        },
        getShowElementModal(state) {
            return state.showElementModal
        },
        getShowRelationModal(state) {
            return state.showRelationModal
        },
        isViewModeRelation(state) {
            return (state.viewModeArchitecture.selected == 'relation')
        },
        isViewModeRequirement(state) {
            return (state.viewModeArchitecture.selected == 'requirement')
        },
        getTableHeaderRelations(state) {
            return state.tableHeaderRelations
        },
        getTableHeaderRequirements(state) {
            return state.tableHeaderRequirements
        },
        getTableHeaderRealizations(state) {
            return state.tableHeaderRealizations
        },
        getTableHeaderRequirementChildren(state) {
            return state.tableHeaderRequirementChildren
        },
        getSelectedLayer(state) {
            return state.selectedLayer
        },
        getPrefixJiraLink(state) {
            return state.appConfig.prefixJiraLink
        },
        getAppConfig(state) {
            return state.appConfig
        },
        getTabletRequirementsCount(state) {
            return state.tabletRequirementsCount
        }
    },

    mutations: {
        setViewMode(state, modeValue) {
            state.viewModeArchitecture.selected = modeValue
        },
        setShowElementModal(state, showValue) {
            state.showElementModal = showValue
        },
        setShowRelationModal(state, showValue) {
            state.showRelationModal = showValue
        },
        setSelectedLayer(state, layerValue) {
            state.selectedLayer = layerValue
                // App.vue: watcher
        },
        setPrefixJiraLink(state, httpLink) {
            state.appConfig.prefixJiraLink = httpLink
        },
        setTabletRequirementsCount(state, count) {
            state.tabletRequirementsCount = count
        }
    },

    state: () => ({
        appConfig: {
            prefixJiraLink: "",
        },
        showElementModal: false,
        showRelationModal: false,
        selectedLayer: "Projekt",

        viewModeArchitecture: {
            selected: 'relation',
            options: [
                { text: 'Abhängigkeiten/Architektur', value: 'relation' },
                { text: 'Requirements', value: 'requirement' },
            ]
        },

        tableHeaderRelations: [{
                key: 'actions',
                label: 'Aktionen',
                sortable: false
            },
            {
                key: 'id',
                sortable: true
            },
            {
                key: 'layer',
                label: 'Ebene',
                sortable: true
            },
            {
                key: 'type',
                label: 'Typ',
                sortable: true
            },
            {
                key: 'tablet',
                sortable: true
            },
            {
                key: 'start',
                sortable: true
            },
            {
                key: 'end',
                label: 'Ende',
                sortable: true
            },
            {
                key: 'title',
                label: 'Titel',
                sortable: true
            },
            {
                key: 'description',
                label: 'Beschreibung',
                sortable: true
            }
        ],

        tabletRequirementsCount: 0,
        tableHeaderRequirements: [{
                key: 'actions',
                label: 'Aktionen',
                sortable: false
            },
            {
                key: 'flags',
                label: 'Tags',
                sortable: false
            },
            {
                key: 'id',
                sortable: true
            },
            {
                key: 'type',
                sortable: true,
            },
            {
                key: 'tablet',
                label: 'IT-Service Tablet',
                sortable: true
            },
            {
                key: 'name',
                label: 'Titel',
                sortable: true
            },
            {
                key: 'description',
                label: 'Beschreibung',
                sortable: true
            },
            {
                key: 'benefit',
                label: 'Mehrwert',
                sortable: true
            },
            {
                key: 'source',
                label: 'Quelle',
                sortable: true
            },
            {
                key: 'sourcedetails',
                label: 'Quellen-Zusatz',
                sortable: true
            },
        ],

        tableHeaderRealizations: [{
                key: 'id',
                label: 'Realization-Id'
            },
            {
                key: 'start',
                label: 'Forderer'
            },
            {
                key: 'action',
                label: 'Aktion'
            },
            {
                key: 'end',
                label: 'Realisierer'
            },
            {
                key: 'commitment',
                label: 'Verbindlichkeit'
            },
            {
                key: 'status'
            },
            {
                key: 'comment',
                label: 'Bemerkung'
            },
        ],

        tableHeaderRequirementChildren: [{
                key: 'actions',
                label: 'Aktionen'
            },
            {
                key: 'id',
            },
            {
                key: 'requirementTablet',
                label: 'Tablet'
            },
            {
                key: 'name',
                label: 'Titel'
            },
            {
                key: 'description',
                label: 'Beschreibung'
            }
        ]
    })
}


