export default {
    getters: {
        commitmentTypes(state) {
            return state.commitment
        },

        requirementTypes(state) {
            return state.requirementTypes
        },

        statusTypes(state) {
            return state.relationStatus
        },

        relationTypes(state) {
            return state.relationTypes
        },

        layers(state) {
            return state.viewLayers.map(l => l.name)
        },

        tabletTypes(state) {
            return state.tabletComponents
        },

        tabletOptions(state) {
            var options = []
            state.tabletComponents.forEach((lab) => {
                lab.options.forEach((opt) => {
                    options.push(opt);
                })
            })
            return options;
        },

        getLabels(state) {
            return state.labels
        }
    },

    mutations: {
        addLabel(state, labelName) {
            if (state.labels.indexOf(labelName) == -1) {
                state.labels.push(labelName)
            }
        },

        deleteLabel(state, labelName) {
            if (state.labels.indexOf(labelName) != -1) {
                state.labels = state.labels.filter(l => l != labelName)
            }
        },

        replaceLabels(state, labelStore) {
            state.labels = labelStore
        }
    },

    state: () => ({
        labels: [],

        tabletComponents: [{
                "label": "Kunde",
                "options": ["Use Case"]
            }, {
                "label": "Service Utility",
                "options": ["Service Utility"]
            },
            {
                "label": "Service Warranty",
                "options": ["Kapazität", "Service Warranty", "Sicherheit", "Verfügbarkeit"]
            },
            {
                "label": "Organisation",
                "options": ["Fähigkeiten", "Kapazitäten", "Organisation"]
            },
            {
                "label": "Prozesse",
                "options": ["Prozesse"]
            },
            {
                "label": "Finanzen",
                "options": ["Finanzen"]
            },
            {
                "label": "Reporting",
                "options": ["Reporting"]
            },
            {
                "label": "Management Systeme",
                "options": ["Management Systeme"]
            },
            {
                "label": "Technik",
                "options": ["Housing", "Informationssicherheit", "Mengengerüst", "Technik", "Technische Funktionalität"]
            },
            {
                "label": "Inter-Service-Beziehungen",
                "options": ["Inter-Service-Beziehungen"]
            }
        ],

        commitment: [
            "Vorgabe",
            "Anforderung",
            "Annahme"
        ],

        requirementTypes: [
            "Funktional",
            "Nicht-Funktional"
        ],

        relationStatus: [
            "aufgenommen",
            "freigegeben",
            "realisiert",
            "zu prüfen",
            "zurückgestellt",
            "ausgeschlossen"
        ],

        relationTypes: {
            "norelation": {
                "name": "no relation",
                "fill": "#dddddd"
            },
            "consume": {
                "name": "consume",
                "fill": "#cd5c5c"
            },
            "dependency": {
                "name": "dependency",
                "fill": "#5c9acd"
            },
            "bidirection": {
                "name": "bi-direction",
                "fill": "#cdb35c"
            },
            "selection": {
                "name": "selection",
                "fill": "#eeee00"
            }
        },

        viewLayers: [{ // sort: high to low
            "name": "Projekt",
        }, {
            "name": "Funktionalität",
        }, {
            "name": "Release",
        }, {
            "name": "Solution",
        }, {
            "name": "Serviceinstanz",
        }, {
            "name": "Applikation",
        }, {
            "name": "Netzwerk",
        }]
    }),

}


