<template>
    <div>
        <b-card title="Realizations">
            <b-card-text><b-table striped hover
                :items="tableRealizations" 
                :fields="getTableHeaderRealizations"
                responsive
                primary-key="id">
                <template #cell(start)="row">
                    <b-form-select v-model="row.item.startId" :options="getddElementsGroupByType" @change="changeStart(row.item.id, row.item.startId)"></b-form-select>
                </template>
                <template #cell(end)="row">
                    <b-form-select v-model="row.item.endId" :options="getddElementsGroupByType" @change="changeEnd(row.item.id, row.item.endId)"></b-form-select>
                </template>
                <template #cell(commitment)="row">
                    <basic-cell-select-badge
                        :selected="row.item.commitment"
                        :options="commitmentTypes"
                        :dataObjectId="row.item.id"
                        @changeItem="changeCommitment" />
                </template>
                <template #cell(status)="row">
                    <basic-cell-select-badge
                        :selected="row.item.status"
                        :options="statusTypes"
                        :dataObjectId="row.item.id"
                        @changeItem="changeStatus" />
                </template>
                <template #cell(comment)="row">
                    <basic-cell-textarea 
                        :textValue="row.item.comment"
                        :dataObjectId="row.item.id"
                        @changeItem="changeComment" />
                </template>
                <template #cell(action)="row">
                    <b-button size="sm" variant="outline-dark" @click="swapStartEnd(row.item.id, row.item.startId, row.item.endId)"><b-icon-arrow-left-right></b-icon-arrow-left-right></b-button>
                </template>
            </b-table></b-card-text>
        </b-card>
        <b-card title="Kinder-Elemente">
            <b-card-text><b-table striped hover
                :items="tableChildRequirements" 
                :fields="getTableHeaderRequirementChildren"
                responsive
                primary-key="id">
                <template #cell(actions)="row">
                    <b-button size="sm" variant="outline-dark" @click="showRequirementDetails(row.item.id)"><b-icon-gear></b-icon-gear></b-button>
                </template>
            </b-table></b-card-text>
        </b-card>
    </div>
</template>

<script>
import { mapGetters, mapMutations } from 'vuex'
import BasicCellSelectBadge from './BasicCellSelectBadge.vue'
import BasicCellTextarea from './BasicCellTextarea.vue'

export default {
    props: {
        requirementId: {
            type: String,
            required: true
        },
    },

    components: {
        BasicCellSelectBadge,
        BasicCellTextarea
    },

    computed: {
        tableRealizations() {
            return this.getRealizationsOfRequirement(this.requirementId);
        },

        tableChildRequirements() {
            return this.getHierarchiesByParentId(this.requirementId).map(h => this.getElementById(h.childId))
        },

        ...mapGetters([
            'getTableHeaderRealizations',
            'getRealizationsOfRequirement',
            'getddElementsGroupByType',
            'statusTypes',
            'getElementById',
            'getTableHeaderRequirementChildren',
            'getHierarchiesByParentId',
            'commitmentTypes'
        ])
    },

    methods: {
        changeStart(relationId, newCellValue) {
            this.changeRelationProp({
                id: relationId,
                prop: 'startId',
                newValue: newCellValue
            })
        },

        changeEnd(relationId, newCellValue) {
            this.changeRelationProp({
                id: relationId,
                prop: 'endId',
                newValue: newCellValue
            })
            var endElement = this.getElementById(newCellValue)
            this.changeRelationProp({
                id: relationId,
                prop: 'layer',
                newValue: endElement.type
            })
        },

        changeStatus(relationId, newCellValue) {
            this.changeRelationProp({
                id: relationId,
                prop: 'status',
                newValue: newCellValue
            })
        },

        changeCommitment(relationId, newCellValue) {
            this.changeRelationProp({
                id: relationId,
                prop: 'commitment',
                newValue: newCellValue
            })
        },

        changeComment(relationId, newCellValue) {
            this.changeRelationProp({
                id: relationId,
                prop: 'comment',
                newValue: newCellValue
            })
        },

        swapStartEnd(relationId, startId, endId) {
            if(endId.length > 0) {
                this.changeStart(relationId, endId);
            };
            if(startId.length > 0) {
                this.changeEnd(relationId, startId);
            };
        },

        showRequirementDetails(requirementId) {
            this.setSelectedRequirementById(requirementId)
            this.setSelectedRelationById("")
            this.setShowRelationModal(true)
        },

        ...mapMutations([
            'changeRelationProp',
            'setSelectedRequirementById',
            'setSelectedRelationById',
            'setShowRelationModal'
        ])
    }
}
</script>

<style scoped>

</style>


