import storeTypes from '../storeTypes'

function setHierarchyTypeValues(hier) {
    var hierarchyObject = {
        parentId: hier.elemid,
        childId: hier.subelemid
    }

    return hierarchyObject
}

function reverseHierarchyTypeValues(hier) {
    var hierarchyObject = {
        elemid: hier.parentId,
        subelemid: hier.childId
    }

    return hierarchyObject
}

function getNestedIds(storageHierarchy, parentId, depthNodeId) {
    var childKeys = storageHierarchy.filter(e => e.parentId == parentId);
    var childCounts = childKeys.length;
    var childsOfParent = [];
    if (childCounts > 0) {
        childsOfParent = childKeys.map((e) => ({
            value: e.childId,
            depth: depthNodeId + 1,
        }));
        for (var c = 0; c < childCounts; c++) {
            childsOfParent = childsOfParent.concat(
                getNestedIds(storageHierarchy, childKeys[c].childId, depthNodeId + 1)
            );
        }
    }

    if (depthNodeId == 0) {
        return childsOfParent.map(function(child) {
            child.parent = parentId;
            return child;
        });
    } else {
        return childsOfParent;
    }
}

export default {
    getters: {
        getReverseHierarchy: (state) => (hierarchyId) => {
            return reverseHierarchyTypeValues(hierarchyId)
        },
        getScopedElementsWithNestedIds(state, getters, rootState) {
            var selectedIds = []
            rootState.elements.scopedElementIds.forEach((id) => {
                selectedIds = selectedIds.concat(
                    getNestedIds(state.hierarchies, id, 0), {
                        value: id,
                        depth: 0,
                        parent: id,
                    }
                )
            })
            return selectedIds
        },
        getHierarchiesByChildId: (state) => (elemId) => {
            return state.hierarchies.filter(h => h.childId == elemId)
        },
        getHierarchiesByParentId: (state) => (elemId) => {
            return state.hierarchies.filter(h => h.parentId == elemId)
        },
        getParentIdByChildId: (state) => (elemId) => {
            var parentHierarchy = state.hierarchies.find(h => h.childId == elemId)
            if (parentHierarchy != undefined) {
                return parentHierarchy.parentId
            }
            return ""
        },
        getParentOfSelectedRequirement(state, getters, rootState, rootGetters) {
            var parentHierarchy = state.hierarchies.find(h => h.childId == rootGetters.getSelectedRequirement.id)
            if (parentHierarchy != undefined) {
                return rootGetters.getElementById(parentHierarchy.parentId)
            }
            return undefined
        },
        getChildrenOfSelectedRequirement(state, getters, rootState, rootGetters) {
            var childHierarchies = state.hierarchies.filter(h => h.parentId == rootGetters.getSelectedRequirement.id)
            return childHierarchies.map(h => rootGetters.getElementById(h.childId))
        },
        getAllHierarchies(state) {
            return state.hierarchies
        },
        getReleaseElementOfElement: (state, getters, rootState, rootGetters) => (elementObject) => {
            var indexReleaseType = rootGetters.layers.indexOf('Release')
            var indexCurrentElement = rootGetters.layers.indexOf(elementObject.type)

            var currentElement = elementObject
            var parentId = ""
            var noParent = false
            while (indexCurrentElement > indexReleaseType && !noParent) {
                parentId = getters.getParentIdByChildId(currentElement.id)
                if (parentId.length == 0) { // abort
                    noParent = true
                }
                currentElement = rootGetters.getElementById(parentId)
                if (currentElement != undefined) { // external element
                    indexCurrentElement = rootGetters.layers.indexOf(currentElement.type)
                } else {
                    noParent = true
                }
            }

            if (indexCurrentElement == indexReleaseType) { // release
                return currentElement
            }
            return undefined
        }
    },

    mutations: {
        replaceHierarchy(state, hier) {
            state.hierarchies = hier.map(h => setHierarchyTypeValues(h))
        },
        addHierarchyObject(state, hierarchyObject) {
            state.hierarchies.push(hierarchyObject)
        },
        removeHierarchyObject(state, hierarchyObject) {
            state.hierarchies = state.hierarchies.filter(h => !(h.parentId == hierarchyObject.parentId && h.childId == hierarchyObject.childId))
        }
    },

    state: () => ({
        hierarchies: []
    }),
}


