<template>
    <div class="row mt-2">
        <div class="col-2">{{ rowLabel }}:</div>
        <div class="col-10"><b-form-tags
            v-model="tagsModel"
            placeholder="getrennt durch [Enter] oder [Leertaste]"
            separator=" "
            @input="itemChanged">
        </b-form-tags></div>
    </div>
</template>

<script>
export default {
    props: {
        rowLabel: {
            type: String,
            required: true
        },

        dataObject: {
            type: Object,
            required: true
        },

        objectProp: {
            type: String,
            required: true
        },
    },
    
    data() {
        return {
            tagsModel: this.dataObject[this.objectProp]
        }
    },

    methods: {
        itemChanged(newVal) {
            this.$emit('propChanged', this.dataObject.id, newVal, this.objectProp);
        },
    }
}
</script>

<style scoped>

</style>


