<template>
    <b-card no-body class="mb-1">
        <b-card-header header-tag="header" class="p-1" role="tab">
            <b-button block v-b-toggle.requirement-parent variant="outline-dark">
                <span>Requirement :: Eltern <b-badge pill :class="{'bg-info': (getParentOfSelectedRequirement != undefined) }">{{ (getParentOfSelectedRequirement != undefined ? "set" : "") }}</b-badge></span>
            </b-button>
        </b-card-header>
        <b-collapse id="requirement-parent" accordion="modal-accordion" role="tabpanel">
        <b-card-body>
        <b-container fluid>
            <b-row class="mt-2 mb-2"><small><b-alert variant="info" show><u>Hinweis:</u> Änderungen werden für alle Realisierungen übernommen</b-alert></small></b-row>
            <b-row>
                <b-col>
                    <basic-row-add-link class="mb-2" v-if="getParentOfSelectedRequirement == undefined"
                        :ddItems="possibleRequirements"
                        buttonText="Requirement"
                        :allowNewValue="false"
                        @linkItem="linkParent" />
                </b-col>
            </b-row>
            <b-row v-if="getParentOfSelectedRequirement != undefined">
                <b-col cols="11">
                    <b-card no-body class="m-1 p-2">
                        <b-row class="mt-2">
                            <b-col>
                                <h6><span class="badge bg-primary text-white">{{ getParentOfSelectedRequirement.requirementTablet }}</span></h6>
                            </b-col>
                        </b-row>
                        <basic-row-input
                            rowLabel="Name"
                            :dataObject="getParentOfSelectedRequirement"
                            objectProp="name"
                            :isEditable="false" />
                        <basic-row-textarea
                            rowLabel="Beschreibung"
                            :dataObject="getParentOfSelectedRequirement"
                            objectProp="description"
                            :isEditable="false" />
                    </b-card>
                </b-col>
                <b-col cols="1">
                    <b-button class="mt-1" size="sm" variant="outline-dark" @click="showRequirement(getParentOfSelectedRequirement.id)"><b-icon-gear></b-icon-gear></b-button>
                    <my-button-unlink
                        :item-id="getParentOfSelectedRequirement.id"
                        @unlinkItem="removeParent" />
                </b-col>
            </b-row>
        </b-container>
        </b-card-body>
        </b-collapse>
    </b-card>
</template>

<script>
import { mapGetters, mapMutations } from 'vuex';
import BasicRowTextarea from './BasicRowTextarea.vue';
import BasicRowInput from './BasicRowInput.vue';
import BasicRowAddLink from './BasicRowAddLink.vue';
import MyButtonUnlink from './MyButtonUnlink.vue';

export default {
    components: {
        BasicRowTextarea,
        BasicRowInput,
        BasicRowAddLink,
        MyButtonUnlink
    },

    computed: {        
        possibleRequirements() {
            var ddRequirements = []
            var nonRequirementIds = this.getChildrenOfSelectedRequirement.map(r => r.id)
            if(this.getParentOfSelectedRequirement != undefined) {
                nonRequirementIds.push(this.getParentOfSelectedRequirement.id)
            }

            this.getddRequirementsGroupByTablet.forEach((r) => {
                var labelOptions = {
                    label: r.label,
                    options: r.options.filter(ro => nonRequirementIds.indexOf(ro.value) == -1)
                }
                if(labelOptions.options.length > 0) {
                    ddRequirements.push(labelOptions)
                }
            })
            return ddRequirements
        },

        ...mapGetters([
            'getParentIdByChildId',
            'getSelectedRequirement',
            'getElementById',
            'getddRequirementsGroupByTablet',
            'getChildrenOfSelectedRequirement',
            'getParentOfSelectedRequirement'
        ])
    },

    methods: {
        showRequirement(elemId) {
            this.setSelectedRequirementById(elemId)
            this.setSelectedRelationById("")
        },

        linkParent(elementId) {
            this.addHierarchyObject({
                parentId: elementId,
                childId: this.getSelectedRequirement.id
            })
        },

        removeParent(elemId) {
            this.removeHierarchyObject({
                parentId: elemId,
                childId: this.getSelectedRequirement.id
            })
        },

        ...mapMutations([
            'addHierarchyObject',
            'removeHierarchyObject',
            'setSelectedRequirementById',
            'setSelectedRelationById'
        ])
    },
}
</script>

<style scoped>
    
</style>


