<template>
    <b-modal 
        v-model="showModal" 
        scrollable
        size="xl"
        ok-only
        :title="modalTitle"
        @hide="hideModal"
        @blur="hideModal"
        @close="hideModal" >
        <b-container fluid>
            <b-row class="mb-2" v-if="getSelectedRelationIdHistory.length > 0">
                <b-col><b-button variant="outline-dark" @click="showLastView()"><b-icon-arrow-left></b-icon-arrow-left></b-button></b-col> 
            </b-row>
            <b-row>
                <b-col>
                    <div class="accordion" role="tablist">
                        <my-card-selected-relation-general v-if="isRelation"
                             />
                        <my-card-selected-relation-requirement-details v-if="isRequirement"
                             />
                        <my-card-selected-relation-requirement-parent v-if="isRequirement"
                             />
                        <my-card-selected-relation-requirement-children v-if="isRequirement"
                             />
                        <my-card-selected-relation-requirement-relations v-if="isRequirement"
                             />
                        <my-card-selected-relation-requirement-realize v-if="isRequirement"
                             />
                    </div>
                </b-col>
            </b-row>
        </b-container>
    </b-modal>
</template>

<script>
import { mapGetters, mapMutations } from 'vuex'
import MyCardSelectedRelationGeneral from './MyCardSelectedRelationGeneral.vue'
import MyCardSelectedRelationRequirementChildren from './MyCardSelectedRelationRequirementChildren.vue'
import MyCardSelectedRelationRequirementDetails from './MyCardSelectedRelationRequirementDetails.vue'
import MyCardSelectedRelationRequirementRealize from './MyCardSelectedRelationRequirementRealize.vue'
import MyCardSelectedRelationRequirementParent from './MyCardSelectedRelationRequirementParent.vue'
import MyCardSelectedRelationRequirementRelations from './MyCardSelectedRelationRequirementRelations.vue'

export default {
    components: { 
        MyCardSelectedRelationGeneral,
        MyCardSelectedRelationRequirementChildren,
        MyCardSelectedRelationRequirementDetails,
        MyCardSelectedRelationRequirementRealize,
        MyCardSelectedRelationRequirementParent,
        MyCardSelectedRelationRequirementRelations,
    },

    computed: {
        showModal: {
            get() {
                return this.getShowRelationModal
            },
            set(newValue) {
                this.setShowRelationModal(newValue)
            }
        },

        isRequirement() {
            return this.getSelectedRequirement.id != undefined;
        },

        isRelation() {
            return this.getSelectedRelation.id != undefined;
        },

        modalTitle() {
            if(this.isRelation && this.isRequirement) {
                return "Realisierung von " + this.getSelectedRelation.id + " (Requirement " + this.getSelectedRequirement.id + ")";
            } else if(this.isRequirement) {
                return "Requirement " + this.getSelectedRequirement.id;
            } else if(this.isRelation) {
                return "Beziehung " + this.getSelectedRelation.id;
            };
            return "failed";        },

        ...mapGetters([
            'getShowRelationModal',
            'getSelectedRelationIdHistory',
            'getSelectedRequirement',
            'getSelectedRelation'
        ])
    },

    methods: {
        hideModal() {
            this.resetSelectedRelationIdHistory()
            this.setShowRelationModal(false)
        },

        showLastView() {
            this.selectedLastRelation()
        },

        ...mapMutations([
            'setShowRelationModal',
            'resetSelectedRelationIdHistory',
            'selectedLastRelation'
        ])
    }
}
</script>

<style scoped>

</style>


