<template>
    <b-card no-body class="mb-1">
        <b-card-header header-tag="header" class="p-1" role="tab">
            <b-button block v-b-toggle.general-info variant="outline-dark">Allgemein</b-button>
        </b-card-header>
        <b-collapse id="general-info" visible accordion="modal-accordion" role="tabpanel">
        <b-card-body>
        <b-container fluid>
            <basic-row-input
                rowLabel="Beziehungs-Id"
                :dataObject="getSelectedRelation"
                objectProp="id"
                :isEditable="false" />
            <basic-row-input
                rowLabel="Typ"
                :dataObject="getSelectedRelation"
                objectProp="type"
                :isEditable="false" />
            <b-row v-if="!getSelectedRelation.isRealization"><b-col>
                <basic-row-select
                    rowLabel="Ebene"
                    :dataObject="getSelectedRelation"
                    objectProp="layer"
                    :propValues="layers"
                    :isEditable="false" />
                <basic-row-select
                    rowLabel="Tablet"
                    :dataObject="getSelectedRelation"
                    objectProp="tablet"
                    :propValues="tabletTypes"
                    @propChanged="itemChanged" />
                <basic-row-select
                    rowLabel="Start"
                    :dataObject="getSelectedRelation"
                    objectProp="startId"
                    :propValues="ddLayerElements"
                    :isEditable="!getSelectedRelation.isRealization"
                    @propChanged="itemChanged" />
                <basic-row-select
                    rowLabel="Ende"
                    :dataObject="getSelectedRelation"
                    objectProp="endId"
                    :propValues="ddLayerElements"
                    @propChanged="itemChanged" />
                <basic-row-textarea
                    rowLabel="Titel"
                    :dataObject="getSelectedRelation"
                    objectProp="title"
                    @propChanged="itemChanged" />
                <basic-row-textarea
                    rowLabel="Beschreibung"
                    :dataObject="getSelectedRelation"
                    objectProp="description"
                    @propChanged="itemChanged" />
                <basic-row-select
                    rowLabel="Status"
                    :dataObject="getSelectedRelation"
                    objectProp="status"
                    :propValues="statusTypes"
                    @propChanged="itemChanged" />
                <basic-row-textarea
                    rowLabel="Bemerkung"
                    :dataObject="getSelectedRelation"
                    objectProp="comment"
                    @propChanged="itemChanged" />
            </b-col></b-row>
        </b-container>
        </b-card-body>
        </b-collapse>
    </b-card>
</template>

<script>
import { mapGetters, mapMutations } from 'vuex'
import BasicRowSelect from './BasicRowSelect.vue'
import BasicRowInput from './BasicRowInput.vue'
import BasicRowTextarea from './BasicRowTextarea.vue'
import BasicRowDropdownItem from './BasicRowDropdownItem.vue'

export default {    
    components: { 
        BasicRowSelect,
        BasicRowInput,
        BasicRowTextarea,
        BasicRowDropdownItem
    },

    computed: {
        ddLayerElements() {
            if(this.getSelectedRelation.id != undefined) {
                return this.getddElementsGroupByType.find(grp => grp.label == this.getSelectedRelation.layer).options
            }
            return []
        },

        ...mapGetters([
            'getSelectedRelation',
            'layers',
            'tabletTypes',
            'getddElementsGroupByType',
            'statusTypes'
        ])
    },

    methods: {
        itemChanged(objectId, newVal, itemProp) {
            this.changeRelationProp({
                id: objectId,
                prop: itemProp,
                newValue: newVal
            })
        },

        ...mapMutations([
            'changeRelationProp'
        ])
    }
}
</script>

<style scoped>

</style>


