<template>
    <b-card no-body class="mb-1" v-if="upperLayer.length > 0">
        <b-card-header header-tag="header" class="p-1" role="tab">
            <b-button block v-b-toggle.upper-info variant="outline-dark"><strong>Übergeordnete Werte</strong></b-button>
        </b-card-header>
        <b-collapse id="upper-info" :visible="visible">
        <b-card-body>
        <div class="container-fluid">
            <div v-if="releaseElement != undefined">
                <div class="row">
                    <div class="col"><i>{{ releaseElement.name }} (Ebene Release)</i></div>
                </div>
                
                <div class="row">
                    <div class="col-11">
                        <basic-row-start-ende
                            startProp="releaseStart"
                            endProp="releaseEnd" 
                            :dataObject="releaseElement"
                            :isEditable="false" />
                    </div>
                    <div class="col-1">
                        <b-button class="mt-3" size="sm" variant="outline-dark" @click="showElementDetails(releaseElement.id)"><b-icon-gear></b-icon-gear></b-button>      
                    </div>
                </div>
            </div>
        </div>
        </b-card-body>
        </b-collapse>
    </b-card>
</template>

<script>
import { mapGetters, mapMutations } from 'vuex'
import BasicRowStartEnde from './BasicRowStartEnde.vue'

export default {
    props: {
        visible: {
            type: Boolean,
            required: false,
            default: true
        }
    },

    components: {
        BasicRowStartEnde
    },

    computed: {
        upperLayer() {
            var indexDisplayedLayer = this.layers.indexOf(this.getSelectedElement.type);
            
            if(indexDisplayedLayer > 0) {
                return this.layers[indexDisplayedLayer -1];
            }
            return "";
        },

        releaseElement() {
            return this.getReleaseElementOfElement(this.getSelectedElement)
        },

        ...mapGetters([
            'getReleaseElementOfElement',
            'getSelectedElement',
            'layers'
        ])
    },

    methods: {
        showElementDetails(elemId) {
            this.setSelectedElementById(elemId)
        },

        ...mapMutations([
            'setSelectedElementById'
        ])
    }
}
</script>

<style scoped>

</style>


