<template>
    <b-card no-body class="mb-1">
        <b-card-header header-tag="header" class="p-1" role="tab">
            <b-button block v-b-toggle.requirement-targets variant="outline-dark">
                <div>Requirement :: Realisierung <b-badge pill class="bg-info">{{ (getRealizationsOfSelectedRequirement.length > 0 ? getRealizationsOfSelectedRequirement.length : "") }}</b-badge></div>
            </b-button>
        </b-card-header>
        <b-collapse id="requirement-targets" accordion="modal-accordion" role="tabpanel">
        <b-card-body>
        <b-container fluid>
            <basic-row-add-link
                :ddItems="ddNonRequirements"
                buttonText="Relation"
                :allowNewValue="false"
                @linkItem="addRealization"
            />
            <b-card no-body class="m-1 p-2" v-for="(rel, ind) in getRealizationsOfSelectedRequirement" :key="rel.id">
                <b-row class="mt-2 mb-2">
                    <b-col>
                        <b-button v-if="!isSelectedRelation(rel.id)" size="sm" variant="outline-dark" @click="showRelatedRealization(rel.id)"><b-icon-gear></b-icon-gear></b-button>
                        <basic-button-delete
                            :itemId="rel.id"
                            @deleteItem="deleteRealization" />
                    </b-col>
                </b-row>
                <basic-row-dropdown-item
                    rowLabel="Start"
                    :ddElements="ddNonRequirementsWithNoOption"
                    :selectedItemId="rel.startId"
                    :isEditable="isSelectedRelation(rel.id)"
                    @changeItem="changeStart" />
                <basic-row-dropdown-item
                    rowLabel="Ende"
                    :ddElements="ddNonRequirementsWithNoOption"
                    :selectedItemId="rel.endId"
                    :isEditable="isSelectedRelation(rel.id)"
                    @changeItem="changeEnd" />
                <basic-row-select
                    rowLabel="Verbindlichkeit"
                    :dataObject="rel"
                    objectProp="commitment"
                    :propValues="commitmentTypes"
                    @propChanged="itemChanged" />
                <basic-row-select
                    rowLabel="Status"
                    :dataObject="rel"
                    objectProp="status"
                    :propValues="statusTypes"
                    :isEditable="isSelectedRelation(rel.id)"
                    @propChanged="itemChanged" />
                <basic-row-dropdown-item
                    rowLabel="Realisiert"
                    :ddElements="ddNonRequirementsWithNoOption"
                    :ddRelations="getddRelationsGroupByLayer"
                    :selectedItemId="rel.solved"
                    :isEditable="isSelectedRelation(rel.id)"
                    @changeItem="changeSolved" />
                <basic-row-textarea
                    rowLabel="Bemerkung"
                    :dataObject="rel"
                    objectProp="comment"
                    :isEditable="isSelectedRelation(rel.id)"
                    @propChanged="itemChanged" />
                <basic-row-date
                    rowLabel="realisiert bis"
                    :dataObject="rel"
                    objectProp="requirementEnddate"
                    @propChanged="itemChanged" />
                <b-row v-if="startReleases[ind] != undefined || endReleases[ind] != undefined">
                    <b-col cols="3"></b-col>
                    <b-col cols="4" v-if="startReleases[ind] != undefined"><small>Release-Start: {{ startReleases[ind].releaseStart + " - " + startReleases[ind].releaseEnd }}</small></b-col>
                    <b-col cols="4" v-if="endReleases[ind] != undefined"><small>Release-Ende: {{ endReleases[ind].releaseStart + " - " + endReleases[ind].releaseEnd }}</small></b-col>
                    <b-col cols="1"></b-col>
                </b-row>
                <basic-row-url v.if="prefixUrl.length > 0"
                    rowLabel="Jira-Link"
                    :prefixUrl="getPrefixJiraLink"
                    :dataObject="rel"
                    objectProp="jiralink"
                    @propChanged="itemChanged" />
            </b-card>
        </b-container>
        </b-card-body>
        </b-collapse>
    </b-card>
</template>

<script>
import BasicRowAddLink from './BasicRowAddLink.vue'
import BasicRowTextarea from './BasicRowTextarea.vue'
import BasicRowDropdownItem from './BasicRowDropdownItem.vue'
import BasicRowSelect from './BasicRowSelect.vue'
import BasicRowDate from './BasicRowDate.vue'
import BasicRowUrl from './BasicRowUrl.vue'
import BasicButtonDelete from './BasicButtonDelete.vue'
import { mapGetters, mapMutations } from 'vuex'

export default {
    components: {
        BasicRowAddLink,
        BasicRowTextarea,
        BasicRowDropdownItem,
        BasicRowSelect,
        BasicButtonDelete,
        BasicRowDate,
        BasicRowUrl
    },

    computed: {
        ddNonRequirements() {
            return this.getddElementsGroupByType.filter(e => this.layers.indexOf(e.label) != -1)
        },
        
        ddNonRequirementsWithNoOption() {
            var noOption = [{ 
                label: "keine Auswahl",
                options: ['']
            }];

            return noOption.concat(this.ddNonRequirements);
        },

        startReleases() {
            var releases = []
            this.getRealizationsOfSelectedRequirement.forEach((elem) => {
                var startElement = this.getElementById(elem.startId)
                if(startElement != undefined) {
                    releases.push(this.getReleaseElementOfElement(startElement))
                } else {
                    releases.push(undefined)
                }
            })
            return releases
        },

        endReleases() {
            var releases = []
            this.getRealizationsOfSelectedRequirement.forEach((elem) => {
                var endElement = this.getElementById(elem.endId)
                if(endElement != undefined) {
                    releases.push(this.getReleaseElementOfElement(endElement))
                } else {
                    releases.push(undefined)
                }
            })
            return releases
        },

        ...mapGetters([
            'getRealizationsOfSelectedRequirement',
            'getddElementsGroupByType',
            'getSelectedRequirement',
            'getElementById',
            'getSelectedRelation',
            'statusTypes',
            'getddRelationsGroupByLayer',
            'getPrefixJiraLink',
            'getReleaseElementOfElement',
            'layers',
            'commitmentTypes'
        ])
    },

    methods: {
        isSelectedRelation(relationId) {
            if(this.getSelectedRelation.id == undefined) {
                return false
            }
            if(relationId == this.getSelectedRelation.id) {
                return true
            }
            return false
        },

        addRealization(endRelationId) {
            this.addRelation({
                startId: this.getSelectedRequirement.requirementSource,
                endId: endRelationId,
                layer: this.getElementById(endRelationId).type,
                requirementId: this.getSelectedRequirement.id
            })
        },

        showRelatedRealization(relationId) {
            this.setSelectedRelationById(relationId)
        },

        changeStart(itemId) {
            this.changeRelationProp({
                id: this.getSelectedRelation.id,
                prop: 'startId',
                newValue: itemId
            })
        },

        changeEnd(itemId) {
            this.changeRelationProp({
                id: this.getSelectedRelation.id,
                prop: 'endId',
                newValue: itemId
            })
            var endElement = this.getElementById(itemId)
            this.changeRelationProp({
                id: this.getSelectedRelation.id,
                prop: 'layer',
                newValue: endElement.type
            })
        },

        changeSolved(itemId) {
            this.changeRelationProp({
                id: this.getSelectedRelation.id,
                prop: 'solved',
                newValue: itemId
            })
        },

        itemChanged(objectId, newVal, itemProp) {
            this.changeRelationProp({
                id: objectId,
                prop: itemProp,
                newValue: newVal
            })
        },
        
        deleteRealization(relationId) {
            this.deleteRelationById(relationId)
        },

        ...mapMutations([
            'addRelation',
            'deleteRelationById',
            'setSelectedRelationById',
            'changeRelationProp'
        ])
    }
}
</script>

<style scoped>

</style>


