<template>
    <div class="row mt-2">
        <div class="col-2">{{ rowLabel }}:</div>
        <div class="col-10">
            <div class="row">
                <div v-show="!showEdit" class="col-11"><b-form-input :value="formInputName" disabled></b-form-input></div>
                <div v-show="isEditable & !showEdit" class="col-1"><b-button size="sm" variant="outline-dark" @click="showEdit = !showEdit"><b-icon-gear></b-icon-gear></b-button></div>
                <div v-show="showEdit" class="col-1"><b-button size="sm" variant="outline-dark" @click="showEdit = !showEdit"><b-icon-arrow-left></b-icon-arrow-left></b-button></div>
                <div v-if="showEdit" class="col-4"><b-form-select v-model="itemLayer" :options="layers"></b-form-select></div>
                <div v-if="showEdit" class="col-7"><b-form-select v-model="itemId" :options="ddItems" @change="changeItem()"></b-form-select></div>
            </div>
        </div>
    </div>
</template>

<script>
import { mapGetters } from 'vuex';
export default {
    props: {
        rowLabel: {
            type: String,
            required: false,
            default: "Wert"
        },
        ddElements: {
            type: Array,
            required: false,
            default: () => []
        },
        ddRelations: {
            type: Array,
            required: false,
            default: () => []
        },
        selectedItemId: {
            //type: String,
            required: true
        },
        isEditable: {
            type: Boolean,
            required: false,
            default: () => true
        }
    },

    data() {
        return {
            showEdit: false,
            itemId: "",
            itemName: "",
            itemLayer: ""
        }
    },

    computed: {
        ddItems() {
            var ddItems = [{
                label: 'keine Auswahl',
                options: ['']
            }];
            if(this.ddElements.length > 0 && this.itemLayer.length > 0) {
                var ddElementOptions = {
                    label: 'Elements',
                    options: []
                };
                var layerElements = this.ddElements
                    .find(e => e.label == this.itemLayer)
                if(layerElements != undefined) {
                    ddElementOptions.options = layerElements.options
                }
                ddItems.push(ddElementOptions);
            };

            if(this.ddRelations.length > 0 && this.itemLayer.length > 0) {
                var ddRelationOptions = {
                    label: 'Relations',
                    options: []
                };
                var layerRelations = this.ddRelations
                    .find(e => e.label == this.itemLayer)
                if(layerRelations != undefined) {
                    ddRelationOptions.options = layerRelations.options
                }
                ddItems.push(ddRelationOptions);
            };

            return ddItems;
        },

        formInputName() {
            if(this.itemName.length > 0) {
                return this.itemLayer + ': ' + this.itemName;
            }
            return "no linked item";
        },

        ...mapGetters([
            'layers'
        ])
    },

    mounted() {
        this.setValues();
    },

    watch: {
        selectedItemId(newVal) {
            this.setValues();
        }
    },

    methods: {
        setValues() {
            this.itemLayer = "";
            this.itemId = "";
            this.itemName = "";
            if(this.selectedItemId != undefined && this.selectedItemId.length > 0) {
                this.itemId = this.selectedItemId;
                var item = this.ddElements.find((lab) => {
                    var labelOptions = lab.options.find(o => o.value == this.selectedItemId)
                    if(labelOptions != undefined) {
                        this.itemLayer = lab.label
                        this.itemName = labelOptions.text
                        return true
                    }
                    return false
                });
                if(item == undefined) { 
                    var item = this.ddRelations.find((lab) => {
                        var labelOptions = lab.options.find(o => o.value == this.selectedItemId)
                        if(labelOptions != undefined) {
                            this.itemLayer = lab.label
                            this.itemName = labelOptions.text
                            return true
                        }
                        return false
                    });
                };
            };
        },

        changeItem() {
            this.$emit('changeItem', this.itemId);
            this.showEdit = false;
        }
    }
}
</script>

<style scoped>

</style>


