import methodsGeneral from '../../js/methodsGeneral.js'

function getRandomElementId(elementStorage) {
    var randomNumber;
    do {
        randomNumber = Math.floor((Math.random() * 1000000) + 1);
    } while (elementStorage.map(r => r.id).indexOf("ELM" + randomNumber) != -1);

    return "ELM" + randomNumber;
};

function setElementTypeValues(elem) {
    var elementObject = {
        id: elem.id,
        name: elem.name,
        type: elem.type,
        hasFunctions: false
    }

    elementObject.description = methodsGeneral.getJSONObjectValueByName(elem, 'description')

    elementObject.isRelease = (elementObject.type == 'Release')
    if (elementObject.isRelease) {
        elementObject.releaseStart = methodsGeneral.getJSONObjectValueByName(elem, 'releaseStart')
        elementObject.releaseEnd = methodsGeneral.getJSONObjectValueByName(elem, 'releaseEnd')
    }

    elementObject.isRequirement = (elementObject.type == 'Requirement')
    if (elementObject.isRequirement) {
        elementObject.requirementTablet = methodsGeneral.getJSONObjectValueByName(elem, 'tablet')
        elementObject.requirementSource = methodsGeneral.getJSONObjectValueByName(elem, 'source')
        elementObject.requirementSourceDetails = methodsGeneral.getJSONObjectValueByName(elem, 'sourceDetails')
        elementObject.requirementType = methodsGeneral.getJSONObjectValueByName(elem, 'type')
        elementObject.requirementBenefit = methodsGeneral.getJSONObjectValueByName(elem, 'benefit')
        elementObject.requirementLabels = methodsGeneral.getJSONObjectValueByName(elem, 'labels', 'Array')
    }

    elementObject.isServiceinstanz = (elementObject.type == 'Serviceinstanz')
    if (elementObject.isServiceinstanz) {
        elementObject.hasFunctions = true
        elementObject.functions = methodsGeneral.getJSONObjectValueByName(elem, 'functions')
    }

    if (elem.timestamp != undefined) {
        elementObject.timestamp = elem.timestamp
    } else {
        elementObject.timestamp = methodsGeneral.getJSONObjectValueByName(elem, 'timestamp')
    }

    return elementObject
}

function reverseElementTypeValues(elem) {
    var elementObject = {
        id: elem.id,
        name: elem.name,
        type: elem.type
    }
    elementObject.values = []

    elementObject.values.push({
        name: 'description',
        value: elem.description
    })
    elementObject.values.push({
        name: 'timestamp',
        value: elem.timestamp
    })

    if (elem.isRelease) {
        elementObject.values.push({
            name: 'releaseStart',
            value: elem.releaseStart
        })
        elementObject.values.push({
            name: 'releaseEnd',
            value: elem.releaseEnd
        })
    }

    if (elem.isRequirement) {
        elementObject.values.push({
            name: 'tablet',
            value: elem.requirementTablet
        })
        elementObject.values.push({
            name: 'source',
            value: elem.requirementSource
        })
        elementObject.values.push({
            name: 'sourceDetails',
            value: elem.requirementSourceDetails
        })
        elementObject.values.push({
            name: 'commitment',
            value: elem.requirementCommitment
        })
        elementObject.values.push({
            name: 'type',
            value: elem.requirementType
        })
        elementObject.values.push({
            name: 'benefit',
            value: elem.requirementBenefit
        })
        elementObject.values.push({
            name: 'labels',
            value: elem.requirementLabels
        })
    }

    if (elem.isServiceinstanz) {
        elementObject.values.push({
            name: 'functions',
            value: elem.functions
        })
    }

    return elementObject
}

export default {
    getters: {
        getReverseElement: (state) => (elementId) => {
            return reverseElementTypeValues(elementId)
        },
        getElementById: (state) => (elementId) => {
            return state.elements.find(e => e.id == elementId)
        },
        getElementsByType: (state) => (elementType) => {
            return state.elements.filter((e) => e.type == elementType)
        },
        getAllElements(state) {
            return state.elements
        },
        getAllRequirements(state) {
            return state.elements.filter(e => e.isRequirement)
        },
        getScopedElementIds(state) {
            return state.scopedElementIds
        },
        getScopedArgumentOnlyScopedInternal(state) {
            return state.scopedOnlyScopeInternal
        },
        getScopedArgumentWithElementInternal(state) {
            return state.scopedWithElementInternal
        },
        getScopedRequirements(state, getters, rootState, rootGetters) {
            var nestedElementIds = rootGetters.getScopedElementsWithNestedIds.map(e => e.value)
            var requirementElements = []

            // elements as source
            if (!getters.getScopedArgumentOnlyScopedInternal) {
                requirementElements = getters.getAllRequirements.filter(e => nestedElementIds.indexOf(e.requirementSource) != -1)
            }
            // elements in realizations
            if (getters.getScopedArgumentWithElementInternal && !getters.getScopedArgumentOnlyScopedInternal) {
                rootGetters.getAllRealizations.forEach((rel) => {
                    if (nestedElementIds.indexOf(rel.startId) != -1 || nestedElementIds.indexOf(rel.endId) != -1) {
                        requirementElements.push(getters.getElementById(rel.requirementId))
                    }
                })
            } else if (getters.getScopedArgumentWithElementInternal && getters.getScopedArgumentOnlyScopedInternal) {
                rootGetters.getAllRealizations.forEach((rel) => {
                    if (nestedElementIds.indexOf(rel.startId) != -1 && nestedElementIds.indexOf(rel.endId) != -1) {
                        requirementElements.push(getters.getElementById(rel.requirementId))
                    }
                })
            } else if (!getters.getScopedArgumentWithElementInternal && !getters.getScopedArgumentOnlyScopedInternal) {
                rootGetters.getAllRealizations.forEach((rel) => {
                    var startNestedValue = rootGetters.getScopedElementsWithNestedIds.find(e => e.value == rel.startId)
                    var endNestedValue = rootGetters.getScopedElementsWithNestedIds.find(e => e.value == rel.endId)
                    if (startNestedValue != undefined || endNestedValue != undefined) {
                        if (startNestedValue == undefined || endNestedValue == undefined || startNestedValue.parent != endNestedValue.parent) {
                            requirementElements.push(getters.getElementById(rel.requirementId))
                        }
                    }
                })
            } else if (!getters.getScopedArgumentWithElementInternal && getters.getScopedArgumentOnlyScopedInternal) {
                rootGetters.getAllRealizations.forEach((rel) => {
                    var startNestedValue = rootGetters.getScopedElementsWithNestedIds.find(e => e.value == rel.startId)
                    var endNestedValue = rootGetters.getScopedElementsWithNestedIds.find(e => e.value == rel.endId)
                    if (startNestedValue != undefined && endNestedValue != undefined) {
                        if (startNestedValue.parent != endNestedValue.parent) {
                            requirementElements.push(getters.getElementById(rel.requirementId))
                        }
                    }
                })
            }


            /*var nestedRequirements = []
            requirementElements.forEach((req) => {
                var requirementsChildren = rootGetters.getHierarchiesByParentId(req.id).map(h => getters.getElementById(h.childId))
                nestedRequirements = nestedRequirements.concat(req, requirementsChildren)
            })*/
            requirementElements = requirementElements.filter(r => r != undefined) // remove deleted requirements
            return requirementElements.filter((val, ind, _arr) => {
                return _arr.map(r => r.id).indexOf(val.id) === ind
            })
        },
        getSelectedElement(state) {
            return state.selectedElement
        },
        getSelectedRequirement(state) {
            return state.selectedRequirement
        },
        getSelectedElementIdHistory(state) {
            return state.selectedElementIdHistory
        },
        getddElementsGroupByType(state, getters, rootState, rootGetters) {
            var ddElements = state.elements
                .map((elem) => ({
                    value: elem.id,
                    text: elem.name,
                    type: elem.type
                }));
            var groupedElements = d3.nest()
                .key(function(e) { return e.type })
                .entries(ddElements);
            groupedElements = groupedElements.map((elem) => ({
                label: elem.key,
                options: elem.values.sort((a, b) => (a.text < b.text ? -1 : 1))
            }));
            return groupedElements;
        },
        getddRequirementsGroupByTablet(state) {
            var ddElements = state.elements
                .filter(e => e.isRequirement)
                .map((elem) => ({
                    value: elem.id,
                    text: elem.name,
                    tablet: elem.requirementTablet
                }));
            var groupedElements = d3.nest()
                .key(function(e) { return e.tablet })
                .entries(ddElements);
            groupedElements = groupedElements.map((elem) => ({
                label: elem.key,
                options: elem.values.sort((a, b) => (a.text < b.text ? -1 : 1))
            }));
            return groupedElements;
        },
        getLastAddedElement(state, getters) {
            var lastElement = getters.getElementById(state.lastAddedElementId)
            return lastElement
        },
        getElementsByFunctionId: (state) => (functionId) => {
            return state.elements.filter((elem) => elem.functions.indexOf(functionId) != -1)
        }
    },

    mutations: {
        resetScopedElements(state) {
            state.scopedElementIds = []
        },
        addScopeElementId(state, elemId) {
            if (state.scopedElementIds.indexOf(elemId) == -1) {
                state.scopedElementIds.push(elemId)
            } else {
                // click on selected elemId to remove
                state.scopedElementIds.splice(state.scopedElementIds.indexOf(elemId), 1)
            }
        },
        setScopedArgumentOnlyScopedInternal(state, argumentValue) {
            state.scopedOnlyScopeInternal = argumentValue
        },
        setScopedArgumentWithElementInternal(state, argumentValue) {
            state.scopedWithElementInternal = argumentValue
        },
        setSelectedElementById(state, elemId) {
            if (state.selectedElement.id != undefined) {
                state.selectedElementIdHistory.push(state.selectedElement.id)
            }
            state.selectedElement = state.elements.find(elem => elem.id == elemId)
        },
        setSelectedRequirementById(state, elemId) {
            if (elemId.length > 0) {
                state.selectedRequirement = state.elements.find(elem => elem.id == elemId)
            } else {
                state.selectedRequirement = {}
            }
        },
        selectedLastElement(state) {
            if (state.selectedElementIdHistory.length > 0) {
                var lastElementId = state.selectedElementIdHistory.pop()
                state.selectedElement = state.elements.find(e => e.id == lastElementId)
            }
        },
        resetSelectedElementIdHistory(state) {
            state.selectedElementIdHistory = []
        },
        addElement(state, elementObject) {
            state.lastAddedElementId = getRandomElementId(state.elements)
            var newElement = setElementTypeValues({
                id: state.lastAddedElementId,
                name: elementObject.name,
                type: elementObject.type,
                timestamp: methodsGeneral.getCurrentDate()
            })
            state.elements.push(newElement)
        },
        changeElementProp(state, elementObject) {
            var changedElement = state.elements.find(e => e.id == elementObject.id)
            changedElement[elementObject.prop] = elementObject.newValue
            changedElement.timestamp = methodsGeneral.getCurrentDate()
        },
        deleteElementById(state, elementId) {
            state.elements = state.elements.filter(e => e.id != elementId)
        },

        replaceElements(state, elems) {
            state.elements = elems.map(elem => setElementTypeValues(elem))
        },
    },

    state: () => ({
        elements: [],
        scopedElementIds: [],
        scopedOnlyScopeInternal: false,
        scopedWithElementInternal: true,
        selectedElement: {},
        selectedRequirement: {},
        selectedElementIdHistory: [],
        lastAddedElementId: ""
    }),
}


