<template>
    <b-card no-body class="mb-1">
        <b-card-header header-tag="header" class="p-1" role="tab">
            <b-button block v-b-toggle.general-info variant="primary"><strong>Allgemein</strong></b-button>
        </b-card-header>
        <b-collapse id="general-info" :visible="visible">
        <b-card-body>
        <div class="container-fluid">
            <basic-row-select
                rowLabel="Ebene"
                :dataObject="getSelectedElement"
                objectProp="type"
                :propValues="layers"
                :isEditable="false" />
            <basic-row-id-name
                :dataObject="getSelectedElement"
                @changeElement="changeElement" />
            <basic-row-textarea
                rowLabel="Beschreibung"
                :dataObject="getSelectedElement"
                objectProp="description"
                @propChanged="changeElement" />
            <basic-row-start-ende v-if="getSelectedElement.isRelease"
                startProp="releaseStart"
                endProp="releaseEnd"
                :dataObject="getSelectedElement"
                @changeElement="changeElement" />
            <my-row-selected-element-functions
                 />
        </div>
        </b-card-body>
        </b-collapse>
    </b-card>
</template>

<script>
import { mapGetters, mapMutations } from 'vuex'
import MyRowSelectedElementFunctions from './MyRowSelectedElementFunctions.vue'
import BasicRowSelect from './BasicRowSelect.vue'
import BasicRowIdName from './BasicRowIdName.vue'
import BasicRowTextarea from './BasicRowTextarea.vue'
import BasicRowStartEnde from './BasicRowStartEnde.vue'

export default {
    props: {
        visible: {
            type: Boolean,
            required: false,
            default: true
        }
    },

    components: {
        MyRowSelectedElementFunctions,
        BasicRowSelect,
        BasicRowIdName,
        BasicRowTextarea,
        BasicRowStartEnde
    },

    computed: {
        ...mapGetters([
            'getSelectedElement',
            'layers'
        ])

    },

    methods: {
        changeElement(elemId, elemProp, newElemValue) {
            this.changeElementProp({
                id: elemId,
                prop: elemProp,
                newValue: newElemValue
            })
        },

        ...mapMutations([
            'changeElementProp'
        ])
    }
}
</script>

<style scoped>

</style>


