<template>
    <b-card no-body class="mb-1" v-if="parentIdOfSelectedElement.length >0">
        <b-card-header header-tag="header" class="p-1" role="tab">
            <b-button block v-b-toggle.sibling-info variant="outline-primary">
                <span><strong>Geschwister Elemente</strong> :: Ebene {{ getSelectedElement.type }} <b-badge pill class="bg-primary">{{ (elementSiblings.length > 0 ? elementSiblings.length : "") }}</b-badge></span>
            </b-button>
        </b-card-header>
        <b-collapse id="sibling-info" :visible="visible">
        <b-card-body>
        <b-container fluid>
            <basic-row-add-link
                :ddItems="ddLayerElements"
                buttonText="Element"
                @addItem="addSibling"
                @linkItem="linkSibling"
            />
            <b-row class="mt-2" v-for="elem in elementSiblings" :key="elem.id">
                <b-col cols="11">
                    <b-card no-body class="m-1 p-2">
                        <basic-row-id-name
                            :dataObject="elem"
                            :isEditable="false" />
                        <basic-row-start-ende v-if="elem.isRelease"
                            startProp="releaseStart"
                            endProp="releaseEnd"
                            :dataObject="elem"
                            @changeElement="changeElement" />
                        </b-card>
                </b-col>
                <b-col cols="1">
                    <b-button class="mt-1" size="sm" variant="outline-dark" @click="showElementDetails(elem.id)"><b-icon-gear></b-icon-gear></b-button>
                </b-col>
            </b-row>
        </b-container>
        </b-card-body>
        </b-collapse>
    </b-card>
</template>

<script>
import { mapGetters, mapMutations } from 'vuex'
import BasicRowAddLink from './BasicRowAddLink.vue'
import BasicRowStartEnde from './BasicRowStartEnde.vue'
import BasicRowIdName from './BasicRowIdName'

export default {
    props: {
        visible: {
            type: Boolean,
            required: false,
            default: true
        }
    },

    components: {
        BasicRowAddLink,
        BasicRowStartEnde,
        BasicRowIdName
    },

    computed: {
        parentIdOfSelectedElement() {
            return this.getParentIdByChildId(this.getSelectedElement.id)
        },

        elementSiblings() {
            if(this.parentIdOfSelectedElement.length > 0) {
                return this.getHierarchiesByParentId(this.parentIdOfSelectedElement)
                    .filter(h => h.childId != this.getSelectedElement.id)
                    .map(h => this.getElementById(h.childId))
            }
            return []
        },

        ddLayerElements() {
            var layerElements = this.getddElementsGroupByType.find(grp => grp.label == this.getSelectedElement.type).options
            var allChildIds = this.getAllHierarchies.map(e => e.childId)
            var hasNoParentElements = layerElements.filter(e => allChildIds.indexOf(e.value) == -1)
            return hasNoParentElements
        },

        ...mapGetters([
            'getParentIdByChildId',
            'getSelectedElement',
            'getHierarchiesByParentId',
            'getElementById',
            'getddElementsGroupByType',
            'getAllHierarchies',
            'getLastAddedElement'
        ])
    },

    methods: {
        changeElement(elemId, elemProp, newElemValue) {
            this.changeElementProp({
                id: elemId,
                prop: elemProp,
                newValue: newElemValue
            })
        },
        
        showElementDetails(elemId) {
            this.setSelectedElementById(elemId)
        },

        linkSibling(elementId) {
            if(this.parentIdOfSelectedElement.length > 0) {
                this.addHierarchyObject({
                    parentId: this.parentIdOfSelectedElement,
                    childId: elementId
                })
            }
        },

        addSibling(elementName) {
            if(this.parentIdOfSelectedElement.length > 0) {
                this.addElement({
                    name: elementName,
                    type: this.getSelectedElement.type
                })
                var addedElement = this.getLastAddedElement
                this.addHierarchyObject({
                    parentId: this.parentIdOfSelectedElement,
                    childId: addedElement.id
                })
            }
        },

        ...mapMutations([
            'setSelectedElementById',
            'addElement',
            'addHierarchyObject',
            'changeElementProp'
        ])
    }
}
</script>

<style scoped>

</style>


