<template>
    <b-card title="Beziehungen" class="p-3 mb-3">  
        <b-card-text>
            <button type="button" 
                :class="{
                    'btn': true,
                    'btn-sm': true,
                    'mt-3': true,
                    'btn-secondary': layerShowSwitch, 
                    'btn-outline-dark': !layerShowSwitch
                }"
                @click="layerShowSwitch = !layerShowSwitch">Analyse Ebenen</button>
            <table v-show="layerShowSwitch" class="table table-sm table-borderless">
                <tr>
                    <td></td>
                    <td>consume</td>
                    <td>dependency</td>
                </tr>
                <tr v-for="(lay, ind) in $viewLayers" :key="lay.name">
                    <th>{{ lay.name }}</th>
                    <td><span class="badge bg-primary text-white ">{{ getScopedConsumeRelationSumLayer[ind] }}</span></td>
                    <td><span class="badge bg-primary text-white ">{{ getScopedDependencyRelationSumLayer[ind] }}</span></td>
                </tr>
            </table>
            
            <button type="button" 
                :class="{
                    'btn': true,
                    'btn-sm': true,
                    'mt-3': true,
                    'btn-secondary': tableShowSwitch, 
                    'btn-outline-dark': !tableShowSwitch
                }"
                @click="tableShowSwitch = !tableShowSwitch">Analyse Tablet</button>
            <table v-show="tableShowSwitch" class="table table-sm table-borderless">
                <tr>
                    <td></td>
                    <td>consume</td>
                    <td>dependency</td>
                </tr>
                <tr v-for="(tab, ind) in tabletOptions" :key="tab">
                    <th>{{ tab }}</th>
                    <td><span class="badge bg-primary text-white ">{{ getScopedConsumeRelationsSumTablet[ind] }}</span></td>
                    <td><span class="badge bg-primary text-white ">{{ getScopedDependencyRelationsSumTablet[ind] }}</span></td>
                </tr>
            </table>
        </b-card-text>
    </b-card>
</template>

<script>
import { mapGetters } from 'vuex'

export default {
    data() {
        return {
            layerShowSwitch: true,
            tableShowSwitch: false
        }
    },

    computed: {
        ...mapGetters([
            'getScopedConsumeRelationsSumTablet',
            'getScopedDependencyRelationsSumTablet',
            'getScopedConsumeRelationSumLayer',
            'getScopedDependencyRelationSumLayer',
            'tabletOptions'
        ]),
    },
}
</script>

<style scoped>

</style>


