<template>
    <div class="row mt-2">
        <div class="col-2">{{ rowLabel }}:</div>
        <div class="col-3 text-right text-secondary"><small>{{ prefixUrl }}</small></div>
        <div class="col-6"><b-form-input type="text" v-model="inputModel" @change="itemChanged(objectProp)" :disabled="!isEditable"></b-form-input></div>
        <div class="col-1"><a :href="fullUrlPath" target="_blank"><b-button size="sm" variant="outline-dark"><b-icon-arrow-up-right></b-icon-arrow-up-right></b-button></a></div>
    </div>
</template>

<script>
import { mapGetters } from 'vuex';
export default {
    props: {
        rowLabel: {
            type: String,
            required: true
        },

        prefixUrl: {
            type: String,
            required: true
        },

        dataObject: {
            type: Object,
            required: true
        },

        objectProp: {
            type: String,
            required: true
        },

        isEditable: {
            type: Boolean,
            required: false,
            default: true
        }
    },

    data() {
        return {
            inputModel: ""
        }
    },

    mounted() {
        if(this.dataObject[this.objectProp].length > 0) {
            this.inputModel = this.dataObject[this.objectProp].substring(this.prefixUrl.length, this.dataObject[this.objectProp].length) 
        } else {
            this.inputModel = ""
        }
    },

    computed: {
        fullUrlPath() {
            return this.prefixUrl + this.inputModel
        },
    },

    methods: {
        itemChanged(itemProp) {
            this.$emit('propChanged', this.dataObject.id, this.fullUrlPath, itemProp);
        }
    }
}
</script>

<style scoped>

</style>


