<template>
    <b-row class="w-100 bg-light p-1">
        <b-col cols="3">Datensatz: 
            <basic-input-file-reader
          @load="loadStorage" />  
        </b-col>
        <b-col cols="2">
            <b-row>
                <b-col>Perspektive: </b-col>
                <b-col>
                    <b-form-group><b-form-radio-group
                        v-model="modelView.selected"
                        :options="modelView.options"
                        buttons
                        button-variant="outline-primary"
                        size="sm" >
                    </b-form-radio-group></b-form-group>
                </b-col>
            </b-row>
        </b-col>
        <b-col cols="1"></b-col>
        <b-col cols="3">
            <my-storage-search
                />
        </b-col>
        <b-col cols="1"></b-col>
        <b-col cols="1">
            <b-button class="m-1" size="sm" variant="light" @click="downloadStorage()">
                <b-icon-cloud-arrow-down></b-icon-cloud-arrow-down> Daten/txt
            </b-button>
        </b-col>
        <!--<b-col cols="1">
            <b-button class="m-1" size="sm" variant="light" @click="downloadRequirements()">
                <b-icon-cloud-arrow-down></b-icon-cloud-arrow-down> REQs/csv
            </b-button>
        </b-col>-->
    </b-row>
</template>

<script>
import { mapGetters, mapMutations } from 'vuex'
import BasicInputFileReader from './BasicInputFileReader.vue'
import MyStorageSearch from './MyStorageSearch.vue'
import methodsGeneral from '../js/methodsGeneral.js'

export default {
    components: {
        BasicInputFileReader,
        MyStorageSearch
    },

    computed: {
        modelView: {
            get() {
                return this.getViewModeArchitecture
            },
            set(newValue) {
                this.setViewMode(newValue)
            }
        },

        ...mapGetters([
            'getAllRelations',
            'getAllHierarchies',
            'getAllElements',
            'getAllRequirements',
            'getViewModeArchitecture',
            'getReverseElement',
            'getReverseRelation',
            'getReverseHierarchy',
            'getAppConfig',
            'getLabels'
        ]),
    },

    methods: {
        loadStorage(fileText) {
            try {
                var jsonFile = JSON.parse(fileText)
                this.setStorage(jsonFile)
            }
            catch(err) {
                alert(err.message)
            }
        },

        setStorage(storageItems) {
            this.replaceElements(storageItems.elements)
            this.replaceRelations(storageItems.relations)
            this.replaceHierarchy(storageItems.hierarchy)
            this.setPrefixJiraLink(storageItems.config.prefixJiraLink)
            this.replaceLabels(storageItems.enums.labels)
        },

        downloadStorage() {
            this.optimizeLabels()

            var jsonStorage = {
                config: this.getAppConfig,
                enums: {
                    labels: this.getLabels
                },
                elements: this.getAllElements.map(elem => this.getReverseElement(elem)),
                relations: this.getAllRelations.map(rel => this.getReverseRelation(rel)),
                hierarchy: this.getAllHierarchies.map(hier => this.getReverseHierarchy(hier))
            }
            var encodedUri = encodeURIComponent(JSON.stringify(jsonStorage));
            this.generateFileAndDownload(
                "data:text/csv;charset=utf-8," + encodedUri, 
                'databaseCLReport_' + methodsGeneral.getCurrentDate() + '.txt'
            );
        },

        optimizeLabels() {
            this.getLabels.forEach((lab) => {
                if(this.getAllRequirements.filter(elem => elem.requirementLabels.indexOf(lab) != -1).length == 0) {
                    this.deleteLabel(lab)
                }
            })
        },

        /*downloadRequirements() {
            const newLine = '\n';
            const sep = ";";

            var csvString = ("").concat(
                "Id" + sep,
                "Übergeordnete Id" + sep,
                "Titel" + sep,
                "Beschreibung" + sep,
                "Mehrwert" + sep,
                "Typ" + sep,
                "IT-Service Tablet" + sep,
                "Verbindlichkeit" + sep,
                "zu erfüllen von" + sep,
                "gestellt von" + sep,
                "Quellenzusatz" + sep,
                "Zeitpunkt Vorhaben" + sep,
                "Bemerkung" + newLine);
            
            this.storage.relations
                .filter((rel) => {
                if(rel.values.find(rv => rv.name == 'element') != undefined) {
                    return true;
                }
                return false;
                })
                .forEach((rel) => {
                var requirementId = rel.values.find(rv => rv.name == 'element').value;
                var requirement = this.storage.elements.find(e => e.id == requirementId);

                csvString = csvString.concat(
                    requirement.id + sep,
                    "Übergeordnete Id" + sep,
                    requirement.name + sep,
                    this.getObjectValueByName(requirement, 'description') + sep,
                    "Mehrwert" + sep,
                    "Typ" + sep,
                    this.getObjectValueByName(requirement, 'tablet') + sep,
                    this.getObjectValueByName(requirement, 'commitment') + sep,
                    rel.endId + sep,
                    rel.startId + sep,
                    "Quellenzusatz" + sep,
                    "Zeitpunkt Vorhaben" + sep,
                    this.getObjectValueByName(requirement, 'comment') + newLine);
                });

            this.generateFileAndDownload(
                "data:text/csv;charset=utf-8," + encodeURI(csvString), 
                'CLrequirements_' + this.getCurrentDate() + '.txt'
            );
        },*/

        generateFileAndDownload(stringHref, fileName) {
            var link = document.createElement("a");
            link.setAttribute("href", stringHref);
            link.setAttribute("download", fileName);
            document.body.appendChild(link);

            link.click();
        },

        ...mapMutations([
            'setViewMode',
            'replaceElements',
            'replaceRelations',
            'replaceHierarchy',
            'setPrefixJiraLink',
            'replaceLabels',
            'deleteLabel'
        ]),
    }
}
</script>

<style scoped>

</style>


