<template>
    <b-row class="ml-2">
        <!--<b-alert :show="showAlertMessage" variant="danger" @dismissed="emitClose()" dismissible>
          {{ alertMessage }}
        </b-alert>-->
        <b-container class="mt-3" fluid>
            <b-row>
            <my-modal-selected-relation
                 />
            </b-row>
            <b-row class="mt-3 ml-1" v-show="this.getScopedElementIds.length > 0">
                <b-button variant="outline-dark" @click="newRelation()"><b-icon-node-plus></b-icon-node-plus> Architektur-Beziehung</b-button>
            </b-row>
            <b-row>
                <b-col>
                    <basic-row-add-link class="w-50" v-show="this.getScopedElementIds.length > 0"
                        :ddItems="getddRequirementsGroupByTablet"
                        buttonText="Requirement"
                        @addItem="addRequirement"
                        @linkItem="linkRequirement" />
                </b-col>
            </b-row>
            <b-row class="mt-3">
                <b-table striped hover
                    :items="tableObjects" 
                    :fields="getTableHeaderRelations"
                    primary-key="id">
                    <template #cell(actions)="row">
                        <b-button size="sm" variant="outline-dark" @click="showRelationDetails(row.item)"><b-icon-gear></b-icon-gear></b-button>
                        <basic-button-delete
                            :itemId="row.item.id"
                            @deleteItem="deleteRelation" />
                    </template>
                </b-table>
            </b-row>
        </b-container>
    </b-row>
</template>

<script>
import { mapGetters, mapMutations } from 'vuex'
import MyModalSelectedRelation from './MyModalSelectedRelation.vue'
import BasicButtonDelete from './BasicButtonDelete.vue'
import BasicRowAddLink from './BasicRowAddLink.vue'

export default {
    components: {
        MyModalSelectedRelation,
        BasicButtonDelete,
        BasicRowAddLink
    },

    data() {
        return {
            showAlertMessage: false
        }
    },

    watch: {
        alertMessage(newVal) {
            if(!this.showAlertMessage && newVal.length > 0) {
                this.showAlertMessage = true;
            } else if(newVal.length == 0) {
                this.showAlertMessage = false;
            }
        }
    },

    computed: {
        tableObjects() {
            return this.getScopedRelations.map((rel) => {
                var tableRecord = rel
                tableRecord.type = (rel.isRealization ? "Requirement" : "Architektur")

                tableRecord.start = "<element not found>"
                var startElement = this.getElementById(rel.startId)
                if(startElement != undefined) {
                    tableRecord.start = this.getElementById(rel.startId).name
                }

                tableRecord.end = "<element not found>"
                var endElement = this.getElementById(rel.endId)
                if(endElement != undefined) {
                    tableRecord.end = this.getElementById(rel.endId).name
                }

                if(rel.isRealization) {
                    var requirement = this.getElementById(rel.requirementId)
                    tableRecord.tablet = (requirement != undefined ? requirement.requirementTablet : "")
                    tableRecord.title = (requirement != undefined ? requirement.name : "")
                    tableRecord.description = (requirement != undefined ? requirement.description : "")
                }
                return tableRecord
            })
        },

        onlyScopedInternal: {
            get() {
                return this.getScopedArgumentOnlyScopedInternal
            },
            set(newValue) {
                this.setScopedArgumentOnlyScopedInternal(newValue)
            }
        },

        withElementInternal: {
            get() {
                return this.getScopedArgumentWithElementInternal
            },
            set(newValue) {
                this.setScopedArgumentWithElementInternal(newValue)
            }
        },

        ...mapGetters([
            'getScopedRelations',
            'getElementById',
            'getTableHeaderRelations',
            'getddRequirementsGroupByTablet',
            'getScopedElementIds',
            'getLastAddedElement',
            'getLastAddedRelation',
            'getScopedArgumentOnlyScopedInternal',
            'getScopedArgumentWithElementInternal'
        ])
    },

    methods: {
        showRelationDetails(relationObject) {
            if(relationObject.isRealization) {
                this.setSelectedRelationById(relationObject.id)
                this.setSelectedRequirementById(relationObject.requirementId)
            } else {
                this.setSelectedRelationById(relationObject.id)
                this.setSelectedRequirementById("")
            }
            this.setShowRelationModal(true)
        },

        addRequirement(elementName) {
            if(this.getScopedElementIds.length > 0) {
                this.addElement({
                    name: elementName,
                    type: 'Requirement'
                })

                var newRequirement = this.getLastAddedElement
                this.linkRequirement(newRequirement.id)
            } else {
                // ALERT
            }
        },

        linkRequirement(elementId) {
            if(this.getScopedElementIds.length > 0) {
                var startRelationId = this.getScopedElementIds[0]
                if(this.getScopedElementIds[1] != undefined) {
                    var endRelationId = this.getScopedElementIds[1]
                    var relationLayer = this.getElementById(endRelationId).type
                } else {
                    var endRelationId = ""
                    var relationLayer = ""
                }

                this.addRelation({
                    startId: startRelationId,
                    endId: endRelationId,
                    layer: relationLayer,
                    requirementId: elementId
                })
            } else {
                // ALERT
            }
        },

        newRelation(relationName) {
            if(this.getScopedElementIds.length > 0) {
                var startRelationId = this.getScopedElementIds[0]
                var relationLayer = this.getElementById(this.getScopedElementIds[0]).type
                if(this.getScopedElementIds[1] != undefined) {
                    var endRelationId = this.getScopedElementIds[1]
                } else {
                    var endRelationId = ""
                }

                this.addRelation({
                    startId: startRelationId,
                    endId: endRelationId,
                    layer: relationLayer
                })
                var newRel = this.getLastAddedRelation
                this.changeRelationProp({
                    id: newRel.id,
                    prop: 'title',
                    newValue: relationName
                })

                this.showRelationDetails(newRel)
            } else {
                // ALERT
            }
        },

        deleteRelation(relationId) {
            this.deleteRelationById(relationId)
        },

        ...mapMutations([
            'deleteRelationById',
            'setSelectedRelationById',
            'addElement',
            'addRelation',
            'setShowRelationModal',
            'setSelectedRequirementById',
            'changeRelationProp',
            'setScopedArgumentOnlyScopedInternal',
            'setScopedArgumentWithElementInternal'
        ])
    }
}
</script>

<style scoped>

</style>


